import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Vehicle} from '../../../_models/vehicle';
import {SessionUser} from '../../../_services/session-user';
import {AuthService} from '../../../_services/auth.service';
import {RequestService} from '../../../_services/request.service';
import {AlertifyService} from '../../../_services/alertify.service';
import {Cargo} from '../../../_models/cargo';
import {Request} from "../../../helpers/request.enum";

@Component({
  selector: 'app-cargo-card',
  templateUrl: './cargo-card.component.html',
  styleUrls: ['./cargo-card.component.css']
})
export class CargoCardComponent implements OnInit {
  @Input() cargo: Cargo;
  @Output() changeVehicleOrder = new EventEmitter<number>();
  sessionUser: SessionUser;
  constructor(private authService: AuthService, private requestService: RequestService<any>, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
  }

  public createImgPath = (serverPath: string) => {
    return `http://localhost:52391${serverPath}`;
  }

  delete(id: number) {
    this.alertify.confirm('Are you sure you want to delete this cargo?', () => {
      this.requestService.delete(id, Request.CargoUpdateGetID).subscribe(() => {
        this.alertify.success('Cargo succesfully deleted!');
        this.changeVehicleOrder.emit(id);
      }, error => {
        this.alertify.error(error);
      });
    });
  }

}
