import { Component, OnInit } from '@angular/core';
import {StatisticsService} from "../../_services/statistics.service";
import {Request} from "../../helpers/request.enum";
import {RequestService} from "../../_services/request.service";
import {Freight} from "../../_models/freight";
import {City} from "../../_models/city";
import {CargoReservation} from "../../_models/cargo-reservation";
import {ActivatedRoute} from "@angular/router";
import {ChartDataSets, ChartOptions, ChartType} from "chart.js";
import {Label, SingleDataSet} from "ng2-charts";
import {CommentRating} from "../../_models/commentRating";
import * as pluginDataLabels from "chartjs-plugin-datalabels";
import {SessionUser} from "../../_services/session-user";
import {AuthService} from "../../_services/auth.service";

@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.css']
})
export class StatisticsComponent implements OnInit {
  sessionUser: SessionUser
  myFreights: Freight[];
  cities: City[];
  cargoReservations: CargoReservation[];
  commentRating: CommentRating[];
  monthsNumberList: number[];
  numberOfOccurrences: number[] = [];
  numberOfOccurrencesRating: number[] = [];
  commonCity: string = '';
  FreightHasData: boolean;
  FreightNoData: boolean;

  //bar chart data
  public barChartOptions: ChartOptions = {
    responsive: true,
    // We use these empty structures as placeholders for dynamic theming.
    scales: { xAxes: [{}], yAxes: [{}] },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  public barChartLabels: Label[] = [];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartData: ChartDataSets[] = [];
  //------------------------------------------------------------------
  //pie chart data
  public pieChartOptions: ChartOptions = {
    responsive: true,

  };
  public pieChartLabels: Label[] = ['Rating 1', 'Rating 2', 'Rating 3', 'Rating 4', 'Rating 5'];
  public pieChartData: SingleDataSet = [];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [pluginDataLabels];
  public pieChartColors = [
    {
      backgroundColor: ['rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)', 'rgba(0,0,255,0.3)', 'rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)'],
    },
  ];

  constructor(public statisticsService: StatisticsService, private requestService: RequestService<any>, private route: ActivatedRoute, private authService: AuthService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.route.data.subscribe(data => {
      this.cities = data.city;
      this.commentRating = data.commentRating;
    });
    this.requestService.get(Request.FreightGetNoPage).subscribe(res => {
        this.myFreights = res;
      },error => {},
      () => {
        this.myFreights.length === 0 ? this.FreightNoData = true : this.FreightHasData = true;
        this.statisticsService.doStatistic(this.myFreights);
      });
    if (this.commentRating.length != 0) {
      this.requestService.getQuery(Request.FreightGetNumberMonths, '?GetNumberMonths=true').subscribe(res => {
          this.monthsNumberList = res;
        }, error => {
        },
        () => {

          this.statisticsService.loadBarChartData(this.monthsNumberList);
        });
      this.requestService.getQuery(Request.CargoReservationInsertGet, '?ActiveCarrierFreights=true').subscribe(res => {
          this.cargoReservations = res;
        }, error => {
        },
        () => {
          this.mostVisitedCity();
        });
      this.ratingOccurrences();
    }
  }

  mostVisitedCity() {
    let sumToCheck: number = 0;
    let cityToAssign: string;
    for (let city of this.cities) {
      let countStart = this.cargoReservations.filter(c => c.startLocation === city.name).length;
      let countEnd = this.cargoReservations.filter(c => c.endLocation === city.name).length;
      let sum = countStart + countEnd;
      if (sum > sumToCheck) {
        sumToCheck = sum;
        cityToAssign = city.name;
      }
      this.numberOfOccurrences.push(countStart + countEnd);
    }
    this.commonCity = cityToAssign;
    this.loadBarChart(this.numberOfOccurrences);
  }

  loadBarChart(numberOfOccurences: number[]) {
    this.barChartData = [
      { data: numberOfOccurences, label: 'Most visited cities' }
    ];
    for(let item of this.cities){
      this.barChartLabels.push(item.name);
    }
  }

  ratingOccurrences() {
    let ratingArray = [1,2,3,4,5];
    for (let rating of ratingArray) {
      let count = this.commentRating.filter(c => c.rating === rating).length;
      this.numberOfOccurrencesRating.push(count);
    }
    this.pieChartData = this.numberOfOccurrencesRating;
  }
}
