import {AfterViewInit, Component, OnDestroy, OnInit} from '@angular/core';
import {CargoReservation} from "../../_models/cargo-reservation";
import {ActivatedRoute, Router} from "@angular/router";
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import {GpsService} from "../../_services/gps.service";
import {StatusEnum} from "../../helpers/Status.Enum";
import {Request} from "../../helpers/request.enum";
import {FreightInsertRequest} from "../../_models/requests/freightInsertRequest";
import {CargoReservationInsertRequest} from "../../_models/requests/cargo-reservation-insert-request";
import {RequestService} from "../../_services/request.service";

@Component({
  selector: 'app-carrier-tracking',
  templateUrl: './carrier-tracking.component.html',
  styleUrls: ['./carrier-tracking.component.css']
})
export class CarrierTrackingComponent implements OnInit, AfterViewInit, OnDestroy {
  cargoReservation: CargoReservation;
  s: any;
  startTime: Date;
  startTransformed: any;
  freightInsertRequest: FreightInsertRequest = new FreightInsertRequest();
  cargoReservationInsertRequest: CargoReservationInsertRequest = new CargoReservationInsertRequest();
  isFinished: boolean;

  constructor(private route: ActivatedRoute, private datePipe: DatePipe, private gpsService: GpsService,
              private requestService: RequestService<any>, private router: Router) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.cargoReservation = data.freight;
    });
    this.transformStartTime();
    this.gpsService.InitializeConnection();
    this.gpsService.StartConnection().then(value => console.log(value));
  }

  ngAfterViewInit() {
    setInterval(() => this.start(), 10000);
  }

  ngOnDestroy() {
    this.gpsService.StopConnection().then(value => console.log(value));
  }

  transformStartTime() {
    this.startTime = new Date();
    this.startTransformed = this.datePipe.transform(this.startTime, 'yyyy-MM-dd HH:mm:ss');
  }

  timer() {
    let now  = new Date();
    let nowTransformed = this.datePipe.transform(now, 'yyyy-MM-dd HH:mm:ss');
    let ms = moment(nowTransformed,"YYYY-MM-DD HH:mm:ss").diff(moment(this.startTransformed,"YYYY-MM-DD HH:mm:ss"));
    let d = moment.duration(ms);
    this.s = Math.floor(d.asHours()) + moment.utc(ms).format(":mm:ss");
  }

  start() {
    this.timer();
    this.gpsService.CarrierInvokeGps(this.cargoReservation.cargoID).then(value => console.log(value));

  }

  finishTransport() {
    this.isFinished = true;
    this.freightInsertRequest.statusStr = StatusEnum.Finished;
    this.freightInsertRequest.startTime = this.startTime;
    this.freightInsertRequest.finishTime = new Date();
    this.cargoReservationInsertRequest.statusStr = StatusEnum.Finished;
    this.requestService.update(this.cargoReservation.freightID, this.freightInsertRequest, Request.FreightUpdateGet).subscribe();
    this.requestService.update(this.cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe();
  }

}
