import { Injectable } from '@angular/core';
import {MatDrawer} from '@angular/material/sidenav';

@Injectable({
  providedIn: 'root'
})
export class DrawerNavService {
  private drawer: MatDrawer;

  constructor() { }


  public setDrawer(drawer: MatDrawer) {
    this.drawer = drawer;
  }

  public isOpened()
  {
    if(this.drawer.opened)
        return true;
    else
        return false;
  }

  public open() {
    if (this.drawer === undefined) return;
    return this.drawer.open();
  }


  public close() {
    if (this.drawer === undefined) return;
    return this.drawer.close();
  }

  public toggle(): void {
    if (this.drawer === undefined) return;
    this.drawer.toggle();
  }
}
