import {Injectable} from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';
import {SessionUser} from '../_services/session-user';
import {RequestService} from './request.service';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {User} from '../_models/user';
import {ActivatedRoute, Router} from '@angular/router';
import {Request} from '../helpers/request.enum';
import {tap} from 'rxjs/operators';
import {JwtHelperService} from '@auth0/angular-jwt';
import {AlertifyService} from './alertify.service';
import {ApplicationUserCreateRequest} from '../_models/requests/applicationUserCreateRequest';

@Injectable({
  providedIn: 'root'
})

export class AuthService {
  user = new BehaviorSubject<SessionUser>(null);
  password = new BehaviorSubject<string>(null);
  private tokenExpTimer: any;
  photoUrl = new BehaviorSubject<string>('../../assets/user.png');
  currentPhotoUrl = this.photoUrl.asObservable();
  decodedToken: any;

  constructor(private http: HttpClient, private router: Router, private route: ActivatedRoute) {
  }

  login(email: string, pass: string, rememberMe: boolean): Observable<User> {
    this.password.next(pass);
    return this.http.get<User>(
      Request.URL + Request.Auth, { headers: new HttpHeaders().append('Authorization', 'Basic ' + btoa(email + ':' + pass))})
      .pipe(tap(res => {
        this.handleAuth(res, rememberMe);
      }));
  }
  // tslint:disable-next-line:typedef
  register(user: ApplicationUserCreateRequest)
  {
    return this.http.post(Request.URL + Request.Register, user);
  }

  changeMemberPhoto(photoUrl: string): void {
    this.photoUrl.next(photoUrl);
  }

  private handleAuth(data: User, rememberMe: boolean): void {
    const helper = new JwtHelperService();
    const token = helper.decodeToken(data.token);
    this.decodedToken = token;
    const roles = token.role;
    const expDate = new Date(token.exp * 1000);
    // tslint:disable-next-line:max-line-length
    const user = new SessionUser( roles, data.email, data.username, data.token, expDate, data.photoURL, data.carrier?.rating);
    this.user.next(user);
    // console.log(expiresIn * 1000);
    if (rememberMe) {
      const expiresIn = expDate.getTime() - new Date().getTime();
      this.autoLogout(expiresIn);
    }
    localStorage.setItem('userData', JSON.stringify(this.user.value));
    localStorage.setItem('token', user.Token);
    this.changeMemberPhoto(this.user.value.PhotoURL);
    const redirectRoute = this.route.snapshot.queryParams.redirectRoute ? this.route.snapshot.queryParams.redirectRoute : '/dashboard/home';
    this.router.navigate([redirectRoute]);
  }

  autoLogout(expirationDuration: number): void {
    this.tokenExpTimer =  setTimeout( () => { this.logout(); }, expirationDuration);
  }

  logout(): void {
    this.user.next(null);
    localStorage.removeItem('userData');
    localStorage.removeItem('token');
    this.router.navigate(['/auth/login']);
    if (this.tokenExpTimer) {
      clearTimeout(this.tokenExpTimer);
    }
    this.tokenExpTimer = null;
  }

  autoLogin(): void {
    // tslint:disable-next-line:max-line-length
    const data: {
      Email: string,
      Username: string,
      Role: string,
      _JWT: string,
      PhotoURL: string,
      Rating: number;
      _tokenExpirationDate: Date } = JSON.parse(localStorage.getItem('userData'));


    if (!data) {
      return;
    } else {
      // tslint:disable-next-line:max-line-length
      const loadedUser = new SessionUser(data.Role, data.Email, data.Username,  data._JWT, data._tokenExpirationDate, data.PhotoURL, data.Rating);
      if (loadedUser) {
        this.user.next(loadedUser);
        const expDuration = new Date (loadedUser.ExpDate).getTime() - new Date().getTime();
        this.autoLogout(expDuration);
      }
    }
  }


}
