import { Component, OnInit, Injectable } from '@angular/core';
import { Subject, BehaviorSubject } from 'rxjs';
import {Notification} from "../../_models/notification";
import { DatePipe } from '@angular/common';


@Injectable({
  providedIn: 'root'
})
// @ts-ignore
export class NotiService {
  emitter = new Subject<number>();
  data = new BehaviorSubject<Notification[]>(null);
  //item = new BehaviorSubject<Notification>(null);
  constructor() {
  }
}

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnInit {
  data: Notification[] = []

  constructor(private notiService: NotiService, private datePipe: DatePipe) { }

  ngOnInit(): void {
    this.notiService.data.subscribe(res => {
      this.data.push(...res);
      this.notiService.emitter.next(this.getLenght());
    });

  /*  this.notiService.item.subscribe(res => {
      if (res){
        //this.data.splice(0, 0, res);
        this.data.push(res);
        this.notiService.emitter.next(this.getLenght());
      }
    });*/
  }

  compareDate(x: Date): boolean {
    console.log('hi');
    return this.datePipe.transform(x, 'yyyy-MM-dd') ===  this.datePipe.transform(new Date(), 'yyyy-MM-dd');
  }

  getLenght(): number{
    return this.data.filter(x => this.compareDate(x.time) && !x.open).map(x => x).length;
  }
}
