import {AfterViewInit, Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { DatePipe } from '@angular/common';
import { AuthService } from '../_services/auth.service';
import { HttpClient } from '@angular/common/http';
import {BreakpointObserver} from '@angular/cdk/layout';
import {DrawerNavService} from '../_services/drawer-nav.service';
import {LoadScriptsService} from '../_services/load-scripts.service';
import {SessionUser} from '../_services/session-user';
import {NotificationService} from "../_services/notification.service";



@Component({
  selector: 'app-dashboard',
  templateUrl: 'dashboard.component.html',
  styleUrls: ['dashboard.component.css']
})

export class DashboardComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('drawer') drawer: MatDrawer;
  isSmallScreen: any;
  isOpened = true;
  linkName: string;
  sessionUser: SessionUser;
  mode = '';


  /*isNotificationOpen = false;
  private notifikacijaService: RequestService<Notifikacija>;*/

  // tslint:disable-next-line:max-line-length
  constructor(private datePipe: DatePipe, private authService: AuthService, private http: HttpClient, private notificationSignalService: NotificationService,
              private breakpointObserver: BreakpointObserver, private drawerService: DrawerNavService, private load: LoadScriptsService) {
    /*this.notifikacijaService = new RequestService<Notifikacija>(http, Request.NOTIFIKACIJA);*/
  }
  compareDate(x: Date): boolean {
    return this.datePipe.transform(x, 'yyyy-MM-dd') ===  this.datePipe.transform(new Date(), 'yyyy-MM-dd');
  }
  ngAfterViewInit(): void {
    this.drawerService.setDrawer(this.drawer);
  }
  ngOnInit(): void {
    /*this.notificationSignalService.InitializeNotificationConnection();
    this.notificationSignalService.StartConnection().then(value => console.log(value));*/
    this.linkName = 'Home';
    /*this.notifikacijaService.get().subscribe(res => {
      this.notiService.data.next(res);
      this.notiService.emitter.next( res.filter(x => this.compareDate(x.datum)).map(x => x).length);
    });*/

    this.breakpointObserver.observe(['(max-width: 450px), (max-width: 650px)']).subscribe(obs => {
      if (obs.breakpoints['(max-width: 450px)']){
        this.isSmallScreen = obs.breakpoints['(max-width: 450px)'];
        this.isOpened = false;

        this.mode = this.isSmallScreen ? 'over' : 'side';
      }else{
        this.isSmallScreen = obs.breakpoints['(max-width: 450px)'];
        this.isOpened = false;
        this.mode = this.isSmallScreen ? 'over' : 'side';
      }
      if (obs.breakpoints['(max-width: 650px)']){
        this.isOpened = false;
      }else {
        this.isOpened = true;
      }
    });
  }


  ngOnDestroy(): void {
  }

  changeH2(name: string) {
    this.linkName = name;
  }
 /* onNotificationSwitch(): void {
    this.isNotificationOpen = !this.isNotificationOpen;
  }*/

}
