import {RouterModule, Routes} from '@angular/router';
import {CommonModule} from '@angular/common';
import {NgModule} from '@angular/core';
import {VehiclesComponent} from './vehicles/vehicles.component';
import {ProfileComponent} from './profile/profile.component';
import {DashboardHomeComponent} from './dashboard-home/dashboard-home.component';
import {ProfileResolver} from '../_resolvers/profile.resolver';
import {CountryResolver} from '../_resolvers/country.resolver';
import {VehiclesAddComponent} from './vehicles/vehicles-add/vehicles-add.component';
import {VehiclesDetailComponent} from './vehicles/vehicles-detail/vehicles-detail.component';
import {VehicleDetailResolver} from '../_resolvers/vehicle-detail.resolver';
import {ReservationsComponent} from './reservations/reservations.component';
import {ReservationsCarrierDetailsComponent} from './reservations/reservations-carrier-details/reservations-carrier-details.component';
import {ReservationCarrierDetailsResolver} from '../_resolvers/reservation-carrier-details.resolver';
import {ReservationsAddComponent} from './reservations/reservations-add/reservations-add.component';
import {CargoComponent} from './cargo/cargo.component';
import {CargoAddComponent} from './cargo/cargo-add/cargo-add.component';
import {CargoDetailComponent} from "./cargo/cargo-detail/cargo-detail.component";
import {CargoDetailResolver} from "../_resolvers/cargo-detail.resolver";
import {UserSettingsComponent} from "./profile/user-settings/user-settings.component";
import {UserLogResolver} from "../_resolvers/user-log.resolver";
import {ReservationAddResolver} from "../_resolvers/reservation-add.resolver";
import {MyReservationsComponent} from "./reservations/my-reservations/my-reservations.component";
import {FreightAddComponent} from "./freight/freight-add/freight-add.component";
import {CargoReservationDetailResolver} from "../_resolvers/cargo-reservation-detail.resolver";
import {MyFreightsComponent} from "./freight/my-freights/my-freights.component";
import {ReservationDetailsComponent} from "./reservations/reservation-details/reservation-details.component";
import {PaymentComponent} from "./freight/payment/payment.component";
import {RateComponent} from "./freight/rate/rate.component";
import {ReviewsComponent} from "./freight/reviews/reviews.component";
import {CommentRatingResolver} from "../_resolvers/comment-rating.resolver";
import {CommentRatingIDResolver} from "../_resolvers/comment-ratingID.resolver";
import {StatisticsComponent} from "./statistics/statistics.component";
import {CityResolver} from "../_resolvers/city.resolver";
import {ReportComponent} from "./report/report.component";
import {ChatComponent} from "./chat/chat.component";
import {MessageHeaderResolver} from "../_resolvers/message-header.resolver";
import {ChatUserResolver} from "../_resolvers/chat-user.resolver";
import {ClientGuard} from "../_guards/client.guard";
import {HomeComponent} from "../home/home.component";
import {CarrierTrackingComponent} from "./carrier-tracking/carrier-tracking.component";
import {FreightGetIdResolver} from "../_resolvers/freight-get-id.resolver";
import {ClientTrackingComponent} from "./client-tracking/client-tracking.component";


const routes: Routes = [
  {path: '',pathMatch: 'full' , redirectTo: '/home'  },
  {path: 'home', component: DashboardHomeComponent },
  {path: 'vehicles', component: VehiclesComponent},
  {path: 'vehicles/addVehicle', component: VehiclesAddComponent },
  {path: 'vehicles/:id', component: VehiclesDetailComponent, resolve: {vehicleDetail: VehicleDetailResolver} },
  {path: 'cargos',canActivate: [ClientGuard] ,component: CargoComponent },
  {path: 'cargos/addCargo', component: CargoAddComponent },
  {path: 'cargos/:id', component: CargoDetailComponent, resolve: {cargoDetail: CargoDetailResolver} },
  {path: 'profile', component: ProfileComponent, resolve: {user: ProfileResolver, country: CountryResolver} },
  {path: 'changePassword', component: UserSettingsComponent, resolve: {userLog: UserLogResolver}},
  {path: 'reservations', component: ReservationsComponent },
  {path: 'reservations/carrierDetails/:id', component: ReservationsCarrierDetailsComponent, resolve:{user: ReservationCarrierDetailsResolver, commentRatingID: CommentRatingIDResolver} },
  {path: 'reservations/addReservation', component: ReservationsAddComponent, resolve:{users: ReservationAddResolver} },
  {path: 'reservations/addReservation/:id', component: ReservationsAddComponent, resolve:{users: ReservationAddResolver}},
  {path: 'reservations/myReservations', component: MyReservationsComponent},
  {path: 'reservations/reservationDetails/:id', component: ReservationDetailsComponent, resolve:{cargoReservations: CargoReservationDetailResolver}},
  {path: 'freight/addFreight/:id', component: FreightAddComponent, resolve: {reservations: CargoReservationDetailResolver} },
  {path: 'freight/myFreights', component: MyFreightsComponent },
  {path: 'freight/payment/:id', component: PaymentComponent, resolve:{cargoReservations: CargoReservationDetailResolver} },
  {path: 'freight/rate/:id', component: RateComponent, resolve:{cargoReservations: CargoReservationDetailResolver}  },
  {path: 'freight/review', component: ReviewsComponent, resolve:{commentRating: CommentRatingResolver}  },
  {path: 'statistics', component: StatisticsComponent, resolve:{city: CityResolver, commentRating: CommentRatingResolver}  },
  {path: 'report', component: ReportComponent },
  {path: 'chat', component: ChatComponent, resolve:{header: MessageHeaderResolver} },
  {path: 'chat/:id', component: ChatComponent, resolve:{header: MessageHeaderResolver, user: ChatUserResolver} },
  {path: 'tracking/:id', component: CarrierTrackingComponent, resolve:{freight: CargoReservationDetailResolver} },
  {path: 'clientTrack/:id', component: ClientTrackingComponent, resolve:{freight: CargoReservationDetailResolver} },
];
@NgModule({
  declarations: [],
  imports: [
    RouterModule.forChild(routes),
    CommonModule
  ],
  exports: [RouterModule]
})
// @ts-ignore
export class DashboardRoutingModule { }
