import { Injectable } from '@angular/core';
import * as signalR from '@microsoft/signalr';
import {HubConnection, HubConnectionBuilder, IHttpConnectionOptions, MessageHeaders} from "@microsoft/signalr";
import {AuthService} from "./auth.service";
import {SessionUser} from "./session-user";
import {SignalMessage} from "../_models/signalR/signal-message";
import {MessageHeader} from "../_models/message-header";
import {PaginatedResult} from "../_models/pagination";
import {Vehicle} from "../_models/vehicle";
import {RequestService} from "./request.service";
import {ChatMessage} from "../_models/signalR/chat-message";
import {Subject, Observable} from "rxjs";
import {Request} from "../helpers/request.enum";
import {Message} from "../_models/message";
import {HttpHeaders} from "@angular/common/http";
import {UserStatus} from "../_models/signalR/user-status";
import {User} from "../_models/user";

@Injectable({
  providedIn: 'root'
})
export class ChatService {
  private connection: HubConnection;
  sessionUser: SessionUser;
  private _messageHeader: MessageHeader;
  private _messageHeaderId: number;
  private idCarrier: number;
  public chatMessage: ChatMessage = new ChatMessage();
  private sharedObj = new Subject<ChatMessage>();
  messageList: Message[] = [];
  private userStatus: UserStatus = new UserStatus();

  constructor(private authService: AuthService, private requestService: RequestService<any>) {
  }

  public async StopConnection() {
    try {
      await this.connection.stop();
      /*this.userStatus.id = this._messageHeaderId;
      this.userStatus.status = true;
      await this.connection.invoke("ChangeAvailability", this.userStatus);*/
      console.log("Connection stopped");
    }
    catch (err){
      console.log(err);
    }
  }

  public async StartConnection() {
    try {
      await this.connection.start();
      /*this.userStatus.id = this._messageHeaderId;
      this.userStatus.status = true;
      await this.connection.invoke("ChangeAvailability", this.userStatus);*/
      console.log("Connection started");
    }
    catch (err){
      console.log(err);
    }
  }

  InitializeConnection() {
    this.sessionUser = this.authService.user.value;
    this.connection = new HubConnectionBuilder().withUrl("http://localhost:52391/chat", {
      accessTokenFactory: () => this.sessionUser.Token,
      }).withAutomaticReconnect().build();

  /*  this.connection.on("onChangeAvailibility", (res: UserStatus) => {
      this.UpdateHeader(res);
    });*/

    this.connection.on("onSendMessage", (res: SignalMessage) => {
      if (this._messageHeader?.fromID === res.from || this.idCarrier === res.from)
      {
        this.SendLocalMessage(res.message, this._messageHeader?.username.split('@')[0], res.sent);
      }
     });
  }

  assignMessageHeader(messageHeader?: MessageHeader, idUser?) {
    if (idUser != undefined){
      this.idCarrier = idUser;
      this._messageHeader != undefined ? this._messageHeader = undefined : null;
    }
    else{
      this._messageHeader = messageHeader;
      this._messageHeaderId = messageHeader.messageHeaderID;
    }

  }

  public retrieveMappedObject(): Observable<ChatMessage> {
    return this.sharedObj.asObservable();
  }

  SendMessage(chatMessage: ChatMessage) {
    if (chatMessage === null)
      return;

    this.SendLocalMessage(chatMessage.message, "Me", new Date());

    var signalMessage = new SignalMessage();
    signalMessage.to = this._messageHeader === undefined ? this.idCarrier : null;
    signalMessage.headerId = this._messageHeader === undefined ? null: this._messageHeader.messageHeaderID;
    signalMessage.message = this.chatMessage.message;
    signalMessage.sent = new Date();
    this.connection.invoke("SendMessage", signalMessage).catch(err => console.error(err));
  }

  Destroy() {
    this.sharedObj.unsubscribe();
    this.sharedObj = new Subject<ChatMessage>();
  }

  private SendLocalMessage(message: string, user: string, sendAt?: any): void{
    user = user ?? "";
    this.chatMessage.message = message;
    this.chatMessage.user = user;
    this.chatMessage.sent = sendAt;
    this.sharedObj.next(this.chatMessage);
  }

   async LoadMessages() {
    this.messageList = [];
    if (this._messageHeader === null || this._messageHeader === undefined)
      return;
    this.requestService.getQuery(Request.MessageInsertGet, '?MessageHeaderID=' + this._messageHeaderId).subscribe(response => {
        this.messageList = response;
    }, error => {},
      () => {
        if (this.messageList.length > 0)
        {
          for (let i = this.messageList.length - 1; i >=0; i--){
            this.SendLocalMessage(this.messageList[i].content, this.messageList[i].isMyMessage? 'Me' : this._messageHeader.username.split('@')[0], this.messageList[i].sendAt);
          }
        }
      });

  }


}


