import {Component, EventEmitter, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {SessionUser} from '../../_services/session-user';
import {AuthService} from '../../_services/auth.service';
import {DrawerNavService} from '../../_services/drawer-nav.service';
import {NotiService} from "../notification/notification.component";
import {NotificationService} from "../../_services/notification.service";
import {LoadScriptsService} from "../../_services/load-scripts.service";
import {Observable, Subscription} from "rxjs";

@Component({
  selector: 'app-dashboard-navbar',
  templateUrl: './dashboard-navbar.component.html',
  styleUrls: ['./dashboard-navbar.component.css']
})
export class DashboardNavbarComponent implements OnInit, OnDestroy{
  @Input() linkName;
  isAuth = false;
  photoUrl: string;
  user: SessionUser;


  @Output() onSideNaveSwitch = new EventEmitter<void>();
  @Output() onNotificationSwitchEM = new EventEmitter<void>();
  numberOfNotifications: number = 0;
  constructor(private authService: AuthService, private drawerService: DrawerNavService, private notiService: NotiService,
              private notificationSignalService: NotificationService) { }

  ngOnInit(): void {

    this.notificationSignalService.InitializeNotificationConnection();
    this.isAuth = this.authService.user.value != null;
    this.user = this.authService.user.value;
    this.authService.currentPhotoUrl.subscribe(photoUrl => this.photoUrl = photoUrl);

    this.notiService.emitter.subscribe(res => {
      this.numberOfNotifications = res;
    });
  }

  ngOnDestroy() {
    this.notificationSignalService.Destroy();
    this.notificationSignalService.StopConnection().then(value => console.log(value));
  }


  logout() {

  }

  onBtnClick(): void {
   /* if (this.drawerService.isOpened()) {
      this.drawerService.close();
    } else {
      this.drawerService.open();
    }*/
    this.onSideNaveSwitch.emit();
  }


   onNotificationSwitch(): void {
     this.onNotificationSwitchEM.emit();
   }
}
