import {AfterViewInit, Component, OnInit} from '@angular/core';
import {CargoReservation} from "../../../_models/cargo-reservation";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {Vehicle} from "../../../_models/vehicle";
import {FreightInsertRequest} from "../../../_models/requests/freightInsertRequest";
import {RequestService} from "../../../_services/request.service";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "../../../_services/alertify.service";
import {Request} from "../../../helpers/request.enum";
import {StatusEnum} from "../../../helpers/Status.Enum";
import {CargoReservationInsertRequest} from "../../../_models/requests/cargo-reservation-insert-request";
//import TravelMode = google.maps.TravelMode;
import {MapsAPILoader} from "@agm/core";
//import TravelMode = google.maps.TravelMode;

@Component({
  selector: 'app-freight-add',
  templateUrl: './freight-add.component.html',
  styleUrls: ['./freight-add.component.css']
})
export class FreightAddComponent implements OnInit, AfterViewInit {
  public lat = 43.856430;
  public lng = 18.413029;
  TravelMode: any = null;
  distance: any;
  public origin: any;
  public destination: any;
  public marker: any;
  cargoReservation: CargoReservation;
  addFreightForm: FormGroup;
  vehicles: Vehicle[];
  freightInsert: FreightInsertRequest = new FreightInsertRequest();
  cargoReservationInsertRequest: CargoReservationInsertRequest = new CargoReservationInsertRequest();
  IsUpdate: boolean;

  constructor(private formBuilder: FormBuilder, private requestService: RequestService<any>, private route: ActivatedRoute, private alertify: AlertifyService,
              private router: Router, private mapsAPILoader: MapsAPILoader) {
    this.mapsAPILoader.load().then(() => {
      this.TravelMode = google.maps.TravelMode;
    });


  }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.cargoReservation = data.reservations;
    });
    this.cargoReservation.freightID != null ? this.IsUpdate = true : this.IsUpdate = false;
    this.requestService.get(Request.VehicleGETNOPAGE).subscribe(result => {
      this.vehicles = result;
    });
    this.getDirection();

    this.getDistance();
    this.createAddForm();
  }

  ngAfterViewInit() {
    let timerId = setInterval(() => this.start(), 2000);
  }

 public getDistance() {
    return new google.maps.DistanceMatrixService().getDistanceMatrix({'origins': [this.origin], 'destinations': [this.destination],travelMode: this.TravelMode.DRIVING}, (results: any) => {
      this.distance = results.rows[0].elements[0].distance.value / 1000;
      this.addFreightForm.patchValue({
        distance: this.distance
      });
    });
  }

  getDirection() {
    if (this.cargoReservation.startLocation.includes(';')){
      let start = this.cargoReservation.startLocation.split(';');
      let end = this.cargoReservation.endLocation.split(';');
      this.origin = {lat: Number(start[1]), lng: Number(start[0])};
      this.destination = {lat: Number(end[1]), lng: Number(end[0])};
    }
    else{
      this.origin = this.cargoReservation.startLocation;
      this.destination = this.cargoReservation.endLocation;
    }
  }

  createAddForm() {
    if (!this.IsUpdate){
      this.addFreightForm = this.formBuilder.group({
        transportDate: [null, Validators.required],
        price: ['', Validators.required],
        distance: ['', Validators.required],
        vehicleID: ['', Validators.required],
      });
    }
    else {
      this.addFreightForm = this.formBuilder.group({
        transportDate: [this.cargoReservation?.freight.transportDate, Validators.required],
        price: [this.cargoReservation?.freight.price, Validators.required],
        distance: [this.cargoReservation?.freight.distance, Validators.required],
        vehicleID: [this.cargoReservation?.freight.vehicleID, Validators.required],
      });
    }
  }

  add(): void {
    this.freightInsert = Object.assign({}, this.addFreightForm.value);
    this.freightInsert.cargoReservationID = this.cargoReservation.cargoReservationID;
    this.requestService.insert(this.freightInsert, Request.FreightUpdateGet).subscribe(() => {
      this.alertify.success('Freight added successfully!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.router.navigate(['/dashboard/freight/myFreights']);
    });
  }

  update() {
    this.freightInsert = Object.assign({}, this.addFreightForm.value);
    this.freightInsert.statusStr = StatusEnum.NotAccepted;
    this.cargoReservation.extraServices != null ? this.freightInsert.price += this.cargoReservation.extraServices.price : this.freightInsert.price; //ako je izabrao extra service
    this.requestService.update(this.cargoReservation.freightID, this.freightInsert, Request.FreightUpdateGet).subscribe();
    this.cargoReservationInsertRequest.statusStr = StatusEnum.Accepted;
    this.requestService.update(this.cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe(() => {
      this.alertify.success('Freight updated successfully!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.router.navigate(['/dashboard/freight/myFreights']);
    });
  }
  gps = [
    {lng: 18.274724460555916, lat: 43.83311550745114},
    {lng: 18.27510578116165, lat: 43.8332336072979},
    {lng: 18.275439566881353, lat: 43.833212915050275},
    {lng: 18.275844879047902, lat: 43.83322661997473},
    {lng: 18.276178664767606, lat: 43.8332231267852},
    {lng: 18.276536293072816, lat: 43.83315943827554},
    {lng: 18.276953525877282, lat: 43.83316629074431},
    {lng: 18.27727539095907, lat: 43.83322473899021},
    {lng: 18.277561492817433, lat: 43.83324019097866},
    {lng: 18.277966804983983, lat: 43.83321264627067},
    {lng: 18.278312511341603, lat: 43.833210899675514},
    {lng: 18.278622455785474, lat: 43.83321775213837},
    {lng: 18.278884716368005, lat: 43.83322460460043},
    {lng: 18.27921850208771, lat: 43.83322285800564},
    {lng: 18.27963573407363, lat: 43.833070405620674},
  ];
  i = 0;
  private start() {

    debugger;
    if (this.i>14){
      return;
    }
    this.marker = this.gps[this.i];
    this.i++;
  }
  private startTimeTracking() {

  }
}
