import {AfterViewInit, Component, ElementRef, OnInit, QueryList, ViewChild, ViewChildren} from '@angular/core';
import {Request} from "../../../helpers/request.enum";
import {PaginatedResult, Pagination} from "../../../_models/pagination";
import {CargoReservation} from "../../../_models/cargo-reservation";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "../../../_services/alertify.service";
import {RequestService} from "../../../_services/request.service";
import {Freight} from "../../../_models/freight";
import {SessionUser} from "../../../_services/session-user";
import {FreightInsertRequest} from "../../../_models/requests/freightInsertRequest";
import {CargoReservationInsertRequest} from "../../../_models/requests/cargo-reservation-insert-request";
import {StatusEnum} from "../../../helpers/Status.Enum";
import {MatDrawer} from "@angular/material/sidenav";
import {CommonService} from "../../../_services/common.service";
import {MatDialog} from "@angular/material/dialog";
import {DrawerNavService} from "../../../_services/drawer-nav.service";
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {AuthService} from "../../../_services/auth.service";

@Component({
  selector: 'app-my-freights',
  templateUrl: './my-freights.component.html',
  styleUrls: ['./my-freights.component.css']
})
export class MyFreightsComponent implements OnInit, AfterViewInit {
  pagination: Pagination;
  sessionUser: SessionUser;
  reservationParams: any = {};
  cargoReservation: CargoReservation[];
  freightInsertRequest: FreightInsertRequest = new FreightInsertRequest();
  cargoReservationInsertRequest: CargoReservationInsertRequest = new CargoReservationInsertRequest();
  isSmallScreen: boolean;
  isFullscreen = false;
  HiddenBtn: boolean;
  CargoHasValues: boolean;
  CargoNoValues: boolean;
  @ViewChild('drawer') public drawer: MatDrawer;

  form: FormGroup;
  Data: Array<any> = [
    { name: StatusEnum.Accepted, value: StatusEnum.Accepted, checked: false },
    { name: StatusEnum.Initial, value: StatusEnum.Initial, checked: false },
    { name: StatusEnum.Rated, value: StatusEnum.Rated, checked: false },
    { name: StatusEnum.Payed, value: StatusEnum.Payed, checked: false },
    { name: 'Pending', value: 'NotAcc', checked: false }, //ovdje treba zbog Contains funkcije u API-iju
    { name: StatusEnum.Rejected, value: StatusEnum.Rejected, checked: false },
    { name: StatusEnum.Transporting, value: StatusEnum.Transporting, checked: false },
    { name: StatusEnum.Finished, value: StatusEnum.Finished, checked: false },
  ];
  @ViewChildren("checkboxes") checkboxes: QueryList<ElementRef>;


  constructor(private route: ActivatedRoute, private alertify: AlertifyService, private requestService: RequestService<any>,public dialog: MatDialog,
              private drawerService: DrawerNavService, private fb: FormBuilder, private authService: AuthService,private router: Router ) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.form = this.fb.group({
      checkArray: this.fb.array([], [Validators.required])
    });
    this.setFreightParams();
    this.requestService.getPaginatedResult(1,6,Request.CargoReservationInsertGet, this.reservationParams).subscribe( (res: PaginatedResult<CargoReservation[]>)=> {
      this.cargoReservation = res.result;
      this.pagination = res.pagination;
    }, error => {},
      () => {
        this.cargoReservation.length === 0 ? this.CargoNoValues = true : this.CargoHasValues = true;
      });
  }

  ngAfterViewInit() {
    this.drawerService.setDrawer(this.drawer);
    setTimeout(() => {
      if (this.route.snapshot.firstChild){
        this.drawerService.open();
      }else{
        this.drawerService.close();
      }}, 500);
  }


  onCheckboxChange(e) {
    const checkArray: FormArray = this.form.get('checkArray') as FormArray;
    if (e.target.checked) {
      checkArray.push(new FormControl(e.target.value));

    } else {
      let i: number = 0;
      checkArray.controls.forEach((item: FormControl) => {
        if (item.value == e.target.value) {
          checkArray.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  setFreightParams() {
    this.reservationParams.activeCarrierFreights = true;
    this.reservationParams.statusStrArray = [];
  }

  resetFilters() {
    const checkArray: FormArray = this.form.get('checkArray') as FormArray;
    checkArray.controls.forEach((item: FormControl) => {
      checkArray.clear();
    });
    this.checkboxes.forEach((element) => {
      element.nativeElement.checked = false;
    });
    this.form.reset();
    this.reservationParams.statusStr = '';
    this.load(true);
  }


  startTransport(cargoReservation: CargoReservation) {
    this.freightInsertRequest.statusStr = StatusEnum.Transporting;
    this.freightInsertRequest.startTime = new Date();
    this.cargoReservationInsertRequest.statusStr = StatusEnum.Transporting;
    this.requestService.update(cargoReservation.freightID, this.freightInsertRequest, Request.FreightUpdateGet).subscribe();
    this.requestService.update(cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe();
    cargoReservation.freight.statusStr = StatusEnum.Transporting;
    this.router.navigate(['/dashboard/tracking/', cargoReservation.cargoReservationID]);
  }

  finishTransport(cargoReservation: CargoReservation) {
    this.freightInsertRequest.statusStr = StatusEnum.Finished;
    this.freightInsertRequest.finishTime = new Date();
    this.cargoReservationInsertRequest.statusStr = StatusEnum.Finished;
    this.requestService.update(cargoReservation.freightID, this.freightInsertRequest, Request.FreightUpdateGet).subscribe();
    this.requestService.update(cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe();
    cargoReservation.freight.statusStr = StatusEnum.Finished;
  }

  pageChanged(event: any):void {
    this.pagination.currentPage = event.page
    this.load();
  }

  refuseFreight(freight: CargoReservation) {
    this.alertify.confirm('Are you sure you want to refuse this freight?', () => {
      this.freightInsertRequest.statusStr = StatusEnum.Rejected;
      this.requestService.update(freight.freightID,this.freightInsertRequest, Request.FreightUpdateGet).subscribe();
      this.cargoReservationInsertRequest.isFreightDeletion = true;
      this.requestService.update(freight.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe(() => {
          this.alertify.success('Successfully refused the freight!');
      }, error => {}
      , () => {
          this.cargoReservation.splice(this.cargoReservation.findIndex(p => p.cargoReservationID === freight.cargoReservationID), 1);
        });
    }, () => {
    });
  }

  load(isReset?: boolean){
    isReset === true ? this.reservationParams.statusStrArray = [] : this.reservationParams.statusStrArray = this.form.get("checkArray").value;
    this.requestService.getPaginatedResult(this.pagination.currentPage, this.pagination.itemsPerPage,Request.CargoReservationInsertGet, this.reservationParams).subscribe((res: PaginatedResult<CargoReservation[]>) => {
      this.cargoReservation = res.result;
      this.pagination = res.pagination;
    }, error => {
      this.alertify.error(error);
    });
  }

  //Drawer
  onBtnClickClose(): void {
    this.HiddenBtn = false;
    this.isFullscreen = false;
    this.drawer.opened = false;
  }
  onBtnFullscreen(): void {
    this.isFullscreen = true;

  }
  onBtnClick(): void {
    this.HiddenBtn = true;
    if (this.drawer.opened){
      return;
    }
    this.drawerService.toggle();
  }


}
