import {HomeComponent} from './home/home.component';
import {RouterModule, Routes} from '@angular/router';
import {DashboardComponent} from './dashboard/dashboard.component';
import {VehiclesComponent} from './dashboard/vehicles/vehicles.component';
import {AuthComponent} from './auth/auth.component';
import {AuthGuard} from './_guards/auth.guard';
import {LoginComponent} from './auth/login/login.component';
import {CommonModule} from '@angular/common';
import {NgModule} from '@angular/core';
import {RegisterClientComponent} from './auth/register/register-client/register-client.component';
import {RegisterCarrierComponent} from './auth/register/register-carrier/register-carrier.component';


const appRoutes: Routes = [
  {path: '',pathMatch: 'full' , redirectTo: 'home' },
  {path: 'home', component: HomeComponent },
  {path: 'dashboard', canActivate: [AuthGuard], loadChildren: () => import(`./dashboard/dashboard.module`).then(m => m.DashboardModule), component: DashboardComponent },
  {path: 'auth', component: AuthComponent, children: [
      { path: 'login', component: LoginComponent},
      { path: 'registerClient', component: RegisterClientComponent},
      { path: 'registerCarrier', component: RegisterCarrierComponent},
    ]},
  {path: '**', redirectTo: 'home', pathMatch: 'full' },


];
@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(appRoutes),
    CommonModule
  ],
  exports: [RouterModule]
})
// @ts-ignore
export class AppRoutingModule { }
