import { Component, OnInit } from '@angular/core';
import {User} from '../../../_models/user';
import {AlertifyService} from '../../../_services/alertify.service';
import {ActivatedRoute} from '@angular/router';
import {RequestService} from '../../../_services/request.service';
import {CommentRating} from "../../../_models/commentRating";
import {Request} from "../../../helpers/request.enum";
import {PaginatedResult} from "../../../_models/pagination";

@Component({
  selector: 'app-reservations-carrier-details',
  templateUrl: './reservations-carrier-details.component.html',
  styleUrls: ['./reservations-carrier-details.component.css']
})
export class ReservationsCarrierDetailsComponent implements OnInit {
  userCarrier: User;
  commentRatings: CommentRating[];
  hasDataCommentRating: boolean;

  constructor(private alertify: AlertifyService, private route: ActivatedRoute, private requestService: RequestService<any>) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.userCarrier = data.user;
      this.commentRatings = data.commentRatingID;
    });
    this.commentRatings.length > 0 ? this.hasDataCommentRating = true : this.hasDataCommentRating = false;
    /*this.requestService.getQuery(this.pageNumberCustom, this.pageSizeCustom,Request.UserInsertGet, this.reservationParams).subscribe(res=> {
      this.commentRatings = res.result;
    });*/
  }

}
