import {Component, OnDestroy, OnInit, ViewChild, HostListener, AfterViewInit} from '@angular/core';
import {SessionUser} from '../../_services/session-user';
import {AuthService} from '../../_services/auth.service';
import {User} from '../../_models/user';
import {FormBuilder, FormControl, FormGroup, NgForm, Validators, ValidatorFn} from '@angular/forms';
import {RequestService} from '../../_services/request.service';
import {Observable, ReplaySubject, Subject} from 'rxjs';
import {CityService} from '../../_services/city.service';
import {AlertifyService} from '../../_services/alertify.service';
import {Country} from '../../_models/country';
import {City} from '../../_models/city';
import {take, takeUntil} from 'rxjs/operators';
import {MatSelect} from '@angular/material/select';
import {ActivatedRoute} from '@angular/router';
import {BreakpointObserver, BreakpointState} from '@angular/cdk/layout';
import {Photo} from '../../_models/photo';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import * as globals from '../../helpers/globals';
import { Request } from "../../helpers/request.enum";
import {LoaderService} from "../../_services/loader.service";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit, OnDestroy, AfterViewInit {
  photoUrl: string;
  sessionUser: SessionUser;
  userModel: User;
  //usersPhotos: Photo[];
  userValidation: User;
  countries: Country[];
  cities: City[];
  updateForm: FormGroup;
  date: FormControl;
  gender: string;

  public citiesFilterCtrl: FormControl = new FormControl();
  public citiesCtrl: FormControl = new FormControl();
  public filteredCities: ReplaySubject<City[]> = new ReplaySubject<City[]>(1);
  // tslint:disable-next-line:variable-name
  protected _onDestroy = new Subject<void>();
  @ViewChild('singleSelect', { static: true }) singleSelect: MatSelect;
  @HostListener('window:beforeunload', ['$event'])
  unloadNotification($event: any) {
    if (this.updateForm.dirty) {
      $event.returnValue = true;
    }
  }

  constructor(private authService: AuthService, private reqService: RequestService<any>, private route: ActivatedRoute,
              private alertify: AlertifyService, private cityService: CityService, private formBuilder: FormBuilder, private loaderService: LoaderService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.authService.currentPhotoUrl.subscribe(photoUrl => this.photoUrl = photoUrl);
    this.route.data.subscribe(data => {
      this.userModel = data.user;
      this.countries = data.country;
    });
    //this.usersPhotos = this.userModel.photos;
    this.gender = this.userModel.client === null ? '' : this.userModel.client.gender;
    this.createUpdateForm();
    this.userValidation = Object.assign({}, this.updateForm.value);
    if (this.userModel.client != null) {
      this.date = new FormControl(new Date(this.userModel.client.dateOfBirth));
    }
    this.citiesFilterCtrl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterCities();
      });
  }

  ngAfterViewInit() {
    /*this.loaderService.isLoading.next(false);*/
  }

  update(): void {
    this.userModel = Object.assign({}, this.updateForm.value);
    this.authService.user.value.Username = this.userModel.username;
    if (JSON.stringify(this.userValidation) === JSON.stringify(this.userModel))
    {
      this.alertify.error('The values are the same as the old ones! (Umbasa)');
    } else {
      this.updateForm.reset(this.userModel);
      this.userValidation = this.userModel;
      this.reqService.update(this.authService.decodedToken.nameid, this.userModel, Request.UserUpdateGetID).subscribe(next => {
        this.alertify.success('Profile updated successfully!');
      }, error => {
        this.alertify.error(error);
      });
    }
    this.updateForm.reset(this.userModel);
  }

  updateMainPhoto(photoUrl: string) {
    this.userModel.photoURL = photoUrl;
  }

  createUpdateForm() {
    if (this.sessionUser.Role === 'Carrier'){
      this.updateForm = this.formBuilder.group({
        carrier: this.formBuilder.group({
          carrierName: [this.userModel.carrier === null ? 'null' : this.userModel.carrier.carrierName, Validators.required],
          carrierBusinessEmail: [this.userModel.carrier === null ? 'null' : this.userModel.carrier.carrierBusinessEmail, Validators.required],
          driverLicenceNumber: [this.userModel.carrier === null ? 'null' : this.userModel.carrier.driverLicenceNumber, Validators.required],
          businessNumber: [this.userModel.carrier === null ? 'null' : this.userModel.carrier.businessNumber, Validators.required],
          startupPrice: [this.userModel.carrier === null ? 0.00 : this.userModel.carrier.startupPrice.toFixed(2), Validators.required],
        }),
        address: this.formBuilder.group({
          name: [this.userModel.address.name, Validators.required],
          cityID: [this.userModel.address.cityID],
          city: [this.userModel.address.city],
        }),
        username: [this.userModel.username, [Validators.required, Validators.pattern('^[A-Za-z0-9 ]+$')]],
        email: [this.userModel.email, [Validators.email, Validators.required]],
      });
    } else {
      this.updateForm = this.formBuilder.group({

        client: this.formBuilder.group({
          firstName: [this.userModel.client === null ? 'null' : this.userModel.client.firstName, Validators.required],
          lastName: [this.userModel.client === null ? 'null' : this.userModel.client.lastName, Validators.required],
          dateOfBirth: [this.userModel.client === null ? this.date : this.userModel.client.dateOfBirth, Validators.required],
          jmbg: [this.userModel.client === null ? 'null' : this.userModel.client.jmbg, Validators.required]
        }),
        address: this.formBuilder.group({
          name: [this.userModel.address.name, Validators.required],
          cityID: [this.userModel.address.cityID],
          city: [this.userModel.address.city],
        }),
        username: [this.userModel.username, Validators.required],
        email: [this.userModel.email, [Validators.email, Validators.required]],
      });
    }
  }

  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();
  }

  protected filterCities() {
    if (!this.cities) {
      return;
    }
    // get the search keyword
    let search = this.citiesFilterCtrl.value;
    if (!search) {
      this.filteredCities.next(this.cities.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    this.filteredCities.next(
      this.cities.filter(city => city.name.toLowerCase().indexOf(search) > -1)
    );
  }

  loadCities(id: number, event: any): void {
    this.cityService.getSelectedCities(id).subscribe(res => {
      this.cities = res;
      this.citiesCtrl.setValue(this.cities);
      this.filteredCities.next(this.cities.slice());
    });
    this.filteredCities
      .pipe(take(1), takeUntil(this._onDestroy))
      .subscribe(() => {
        this.singleSelect.compareWith = (a: City, b: City) => a && b && a.cityID === b.cityID;
      });
  }

  assignCity(city: City, event: any): void {
    if (event.isUserInput){
      if (this.sessionUser.Role === 'Client' || this.sessionUser.Role === 'Carrier') {
        this.updateForm.controls.address.patchValue({
          cityID: city.cityID,
          city: city
        });
      }
    }
  }

  public customPatternValid(config: any): ValidatorFn {
    return (control: FormControl) => {
      let urlRegEx: RegExp = config.pattern;
      if (control.value && !control.value.match(urlRegEx)) {
        return {
          invalidMsg: config.msg
        };
      } else {
        return null;
      }
    };
  }
}
