import {AfterViewChecked, Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {User} from "../../_models/user";
import {MessageHeader} from "../../_models/message-header";
import {ChatService} from "../../_services/chat.service";
import {ChatMessage} from "../../_models/signalR/chat-message";
import { Subject } from 'rxjs';
import {takeUntil} from "rxjs/operators";
import {AuthService} from "../../_services/auth.service";
import {Session} from "inspector";
import {SessionUser} from "../../_services/session-user";
import {RequestService} from "../../_services/request.service";
import {Request} from "../../helpers/request.enum";

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit, OnDestroy, AfterViewChecked {
  messageHeaderList: MessageHeader[];
  messageArray: ChatMessage[] = [];
  user: User;
  message: ChatMessage = new ChatMessage();
  private ngUnsubscribe = new Subject();
  sessionUser: SessionUser;

  //validation prototype
  HasConnectionStarted: boolean = false;
  AssignedHeader: boolean = false;
  IsSpecificUser: boolean = false;
  @ViewChild('scroll', { static: true }) scroll: any;

  constructor(private route: ActivatedRoute, private chatService: ChatService, private authService: AuthService,
              private requestService: RequestService<any>) {

  }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.route.data.subscribe(data => {
      this.messageHeaderList = data.header;
      this.user = data.user;
    });
    this.chatService.InitializeConnection();
    this.chatService.StartConnection().then(value => console.log(value));
    if (this.user != undefined){
      let filteredList = this.messageHeaderList.filter((header: MessageHeader) => header.fromID === this.user.userID);
      if (filteredList.length > 0)
        this.assignHeaderAndStartSignalR(filteredList[0], null);
      else{
        this.assignHeaderAndStartSignalR(null, this.user.userID);
        this.IsSpecificUser = true;
      }
    }

  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
    this.chatService.StopConnection().then(value => console.log(value));
  }

  ngAfterViewChecked() {
    this.scroll.nativeElement.scrollTop = this.scroll.nativeElement.scrollHeight;
  }



  assignHeaderAndStartSignalR(messageHeader?: MessageHeader, idUser?: any) {
    this.messageArray = [];
    this.chatService.Destroy();
    this.chatService.assignMessageHeader(messageHeader, idUser);

    this.AssignedHeader = true;
  /*  if (this.HasConnectionStarted === false){
      this.chatService.InitializeConnection();
      this.chatService.StartConnection().then(value => console.log(value));
      this.HasConnectionStarted = true;
    }*/
    this.chatService.LoadMessages().then(value => console.log(value));
    this.chatService.retrieveMappedObject().pipe(takeUntil(this.ngUnsubscribe)).subscribe( (receivedObj: ChatMessage) => { this.addToInbox(receivedObj);});
  }

  addToInbox(obj: ChatMessage) {
    let newObj = new ChatMessage();
    newObj.user = obj.user;
    newObj.message = obj.message;
    newObj.sent = obj.sent;
    this.messageArray.push(newObj);
  }

  send(): void {
    if(this.message) {
      if(this.message.message === ''){
        window.alert("Message is required.");
        return;
      } else {
        this.chatService.SendMessage(this.message);                   // Send the message via a service
        this.message.message = '';
        this.scroll.nativeElement.scrollTop = this.scroll.nativeElement.scrollHeight;
        //refresh chat
        this.requestService.getBackend(Request.MessageHeaderInsertGet).subscribe(res => {
          this.messageHeaderList = res;
        })
      }
    }
  }

}
