import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {CargoReservation} from '../../_models/cargo-reservation';
import {AuthService} from '../../_services/auth.service';
import {PaginatedResult, Pagination} from '../../_models/pagination';
import {SessionUser} from '../../_services/session-user';
import {ActivatedRoute} from '@angular/router';
import {RequestService} from '../../_services/request.service';
import {AlertifyService} from '../../_services/alertify.service';
import { User } from 'src/app/_models/user';
import {pageNumber, pageSize} from "../../helpers/globals";
import {Request} from "../../helpers/request.enum";
import {MatDrawer} from "@angular/material/sidenav";
import {CommonService} from "../../_services/common.service";
import {MatDialog} from "@angular/material/dialog";
import {DrawerNavService} from "../../_services/drawer-nav.service";

@Component({
  selector: 'app-reservations',
  templateUrl: './reservations.component.html',
  styleUrls: ['./reservations.component.css']
})
export class ReservationsComponent implements OnInit {
  reservations: CargoReservation[];
  userCarriers: User[];
  userCarrierParams: any = {};
  reservationParams: any = {};
  pagination: Pagination;
  sessionUser: SessionUser;
  noMoreCar: boolean;
  noMoreRes: boolean;
  pageNumberCustom = 1;
  pageSizeCustom = 8;

  constructor(private authService: AuthService, private route: ActivatedRoute, private requestService: RequestService<any>,
              private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.loadOnInit();

  }



  loadOnInit() {
    if (this.sessionUser.Role === 'Client') {
      this.setUserCarrierParams();
      this.requestService.getPaginatedResult(this.pageNumberCustom, this.pageSizeCustom,Request.UserInsertGet, this.userCarrierParams).subscribe((res: PaginatedResult<User[]>)=> {
        this.userCarriers = res.result;
        this.pagination = res.pagination;
      });
    }
    if (this.sessionUser.Role === 'Carrier') {
      this.setReservationParams();
      this.requestService.getPaginatedResult(this.pageNumberCustom, this.pageSizeCustom, Request.CargoReservationInsertGet).subscribe((res: PaginatedResult<CargoReservation[]>)=> {
        this.reservations = res.result;
        this.pagination = res.pagination;
      },error => {
        this.noMoreRes = true;
      });
    }
  }

  setUserCarrierParams() {
    this.userCarrierParams.orderBy = 'all';
    this.userCarrierParams.needCarrier = true;
  }

  setReservationParams() {
    this.reservationParams.orderBy = 'all';
  }

  pageChanged(event: any):void {
    this.pagination.currentPage = event.page;
    this.pageNumberCustom = event.page;
    this.loadReservations();
  }

  loadReservations(){
    if (this.sessionUser.Role === 'Client') {
      this.userCarrierParams.needCarrier = true;
      this.requestService.getPaginatedResult(this.pageNumberCustom, this.pageSizeCustom,Request.UserInsertGet ,this.userCarrierParams).subscribe(res => {
        this.userCarriers = res.result;
        this.pagination = res.pagination;
      });
    } else {
      this.requestService.getPaginatedResult(this.pagination.currentPage, this.pagination.itemsPerPage, Request.CargoReservationInsertGet, this.reservationParams).subscribe((res: PaginatedResult<CargoReservation[]>)=> {
        this.reservations = res.result;
        this.pagination = res.pagination;
      });
    }
  }

  resetFilters() {
    /*this.vehicleParams.name = '';
    this.vehicleParams.type = '';
    this.loadVehicles();*/
  }

  deleteFromList(id: number) {
    /*for (let i = 5; i <= 30; i+=4){
      if (this.pagination.totalItems === i)
        this.pagination.currentPage -=1;
    }
    this.vehicles.splice(this.vehicles.findIndex(p => p.vehicleID === id), 1);*/
  }


}
