import { Injectable } from '@angular/core';
import {CargoReservation} from "../_models/cargo-reservation";
import {AuthService} from "./auth.service";
import {RequestService} from "./request.service";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "./alertify.service";
import {FreightInsertRequest} from "../_models/requests/freightInsertRequest";
import {CargoReservationInsertRequest} from "../_models/requests/cargo-reservation-insert-request";
import { StatusEnum } from '../helpers/Status.Enum';
import {Request} from "../helpers/request.enum";
import {PaginatedResult} from "../_models/pagination";

@Injectable({
  providedIn: 'root'
})
export class CommonService {
  NoData: boolean;
  HasData: boolean;
  freightInsertRequest: FreightInsertRequest = new FreightInsertRequest();
  cargoReservationInsertRequest: CargoReservationInsertRequest = new CargoReservationInsertRequest();

  constructor(private authService: AuthService, private requestService: RequestService<any>, private route: ActivatedRoute,
              private alertify: AlertifyService, private router: Router) {
  }

  setToFalse() {
    this.NoData = false;
    this.HasData = false;
  }

  updateStatus(cargoReservation: CargoReservation) {
    this.alertify.confirm('Do you want to accept ' + cargoReservation?.freight.price + '$ as a price for this freight?', () => {
      this.freightInsertRequest.statusStr = StatusEnum.Accepted;
      this.freightInsertRequest.clientId = cargoReservation.clientID;
      this.requestService.update(cargoReservation.freightID,this.freightInsertRequest , Request.FreightUpdateGet).subscribe(() => {
        this.alertify.success('Price accepted successfully!');
      });
      cargoReservation.freight.statusStr = StatusEnum.Accepted;
    }, () => {
      this.freightInsertRequest.statusStr = StatusEnum.Rejected;
      this.cargoReservationInsertRequest.statusStr = StatusEnum.NotAccepted;
      this.requestService.update(cargoReservation.freightID,this.freightInsertRequest , Request.FreightUpdateGet).subscribe(() => {
      });
      this.requestService.update(cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe(() => {
        this.alertify.error('Price rejected!');
      });
      cargoReservation.statusStr = StatusEnum.NotAccepted;
      cargoReservation.freight.statusStr = StatusEnum.Rejected;
    });
 }

 updateStatusRedirect(cargoReservation: CargoReservation)
 {
   this.alertify.confirm('Do you want to accept ' + cargoReservation?.freight.price + '$ as a price for this freight?', () => {
     this.freightInsertRequest.statusStr = StatusEnum.Accepted;
     this.requestService.update(cargoReservation.freightID,this.freightInsertRequest , Request.FreightUpdateGet).subscribe(() => {
       this.alertify.success('Price accepted successfully!');
     }, error => {},
       () => {
         this.router.navigate(['/dashboard/reservations/myReservations']);
       });
     cargoReservation.freight.statusStr = StatusEnum.Accepted;
   }, () => {
     this.freightInsertRequest.statusStr = StatusEnum.Rejected;
     this.cargoReservationInsertRequest.statusStr = StatusEnum.NotAccepted;
     this.requestService.update(cargoReservation.freightID,this.freightInsertRequest , Request.FreightUpdateGet).subscribe(() => {
     });
     this.requestService.update(cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID ).subscribe(() => {
       this.alertify.error('Price rejected!');
     }, error => {}
     , () =>
       {
         this.router.navigate(['/dashboard/reservations/myReservations']);
       });
     cargoReservation.statusStr = StatusEnum.NotAccepted;
   });
 }

  public createImgPath = (serverPath: string) => {
    return `http://localhost:52391${serverPath}`;
  }






}
