import { Component, OnInit } from '@angular/core';
import {CommentRating} from "../../../_models/commentRating";
import {AuthService} from "../../../_services/auth.service";
import {RequestService} from "../../../_services/request.service";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "../../../_services/alertify.service";
import {CommonService} from "../../../_services/common.service";
import {Request} from "../../../helpers/request.enum";

@Component({
  selector: 'app-reviews',
  templateUrl: './reviews.component.html',
  styleUrls: ['./reviews.component.css']
})
export class ReviewsComponent implements OnInit {
  commentRatings: CommentRating[];
  constructor(private authService: AuthService, private reqService: RequestService<any>, private route: ActivatedRoute, public commonService: CommonService ) { }

  ngOnInit(): void {
    this.commonService.setToFalse();
    this.route.data.subscribe(data => {
      this.commentRatings = data.commentRating;
    });
    this.commentRatings.length === 0 ? this.commonService.NoData = true : this.commonService.HasData = true;
  }

}
