import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MatDialog} from '@angular/material/dialog';
import {RequestService} from '../../../_services/request.service';
import {AlertifyService} from '../../../_services/alertify.service';
import {Router} from '@angular/router';
import {CargoInsertRequest} from '../../../_models/requests/cargoInsertRequest';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-cargo-add',
  templateUrl: './cargo-add.component.html',
  styleUrls: ['./cargo-add.component.css']
})
export class CargoAddComponent implements OnInit {
  addCargoForm: FormGroup;
  cargo: CargoInsertRequest = new CargoInsertRequest();
  isHidden = false;
  description: string;
  selectedFile: File = null;
  formData: FormData;
  constructor(private formBuilder: FormBuilder, private requestService: RequestService<any>, private alertify: AlertifyService,
              private router: Router, private httpClient: HttpClient  ) { }

  ngOnInit(): void {
    this.createAddForm();

    this.formData = new FormData();
  }

  createAddForm() {
    this.addCargoForm = this.formBuilder.group({
      name: ['', Validators.required],
      weight: [0, Validators.required],
      height: [0, Validators.required],
      width: [0, Validators.required],
      description: ['', Validators.required],
      images: ['', Validators.required]
    });
  }

  public fileChange(event: any) {
    if (event.target.files.length > 0) {
      this.formData.delete('images');
      /*let files = event.target.files;*/
      /*this.formData.append('images', files);*/
      let filesToUpload : File[] = event.target.files;
      Array.from(filesToUpload).map((file, index) => {
        this.formData.append('images', file, file.name);
      });
    }
  }




  add(): void {
    this.formData.append('name', this.addCargoForm.get('name').value);
    this.formData.append('weight', this.addCargoForm.get('weight').value);
    this.formData.append('height', this.addCargoForm.get('height').value);
    this.formData.append('width', this.addCargoForm.get('width').value);
    this.formData.append('description', this.addCargoForm.get('description').value);
    this.requestService.insert(this.formData, '/Cargo').subscribe(() => {
      this.alertify.success('Cargo added successfully!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.router.navigate(['/dashboard/cargos']);
    });
  }

}
