import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router} from '@angular/router';
import {AlertifyService} from '../_services/alertify.service';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {AuthService} from '../_services/auth.service';
import {RequestService} from '../_services/request.service';
import { Request } from "../helpers/request.enum";
import {CommentRating} from "../_models/commentRating";


@Injectable()
export class CommentRatingIDResolver implements Resolve<CommentRating[]> {
  constructor(private requestService: RequestService<any>,
              private authService: AuthService,
              private router: Router, private alertify: AlertifyService){}

  resolve(route: ActivatedRouteSnapshot): Observable<CommentRating[]> {
    return this.requestService.getQuery(Request.CommentRatingInsertGet, '?CarrierID=' + Number(route.paramMap.get('id'))).pipe(
      catchError(error => {
        this.alertify.error('Problem retrieving your data');
        this.router.navigate(['/home']);
        return of(null);
      })
    );
  }

}
