import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Photo } from 'src/app/_models/photo';
import { FileUploader } from 'ng2-file-upload';
import { AuthService } from 'src/app/_services/auth.service';
import {RequestService} from '../../_services/request.service';
import {AlertifyService} from '../../_services/alertify.service';
import {Request} from '../../helpers/request.enum';
import {BreakpointObserver} from '@angular/cdk/layout';
import {Vehicle} from '../../_models/vehicle';
import {User} from "../../_models/user";


@Component({
  selector: 'app-photo-editor',
  templateUrl: './photo-editor.component.html',
  styleUrls: ['./photo-editor.component.css']
})
export class PhotoEditorComponent implements OnInit {
  @Input() userObject: User;
  @Input() vehicleObject: Vehicle;
  @Output() getMemberPhotoChange = new EventEmitter<string>();
  uploader: FileUploader;
  hasBaseDropZoneOver = false;
  currentMain: Photo;

  constructor(private authService: AuthService, private requestService: RequestService<any>,
              private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.initializeUploader();
  }

  fileOverBase(e: any): void {
    this.hasBaseDropZoneOver = e;
  }

  initializeUploader() {
    const urlTest = this.vehicleObject !== undefined ? Request.URL + Request.PhotoInsertGet + '?vehicleId=' + this.vehicleObject.vehicleID  : Request.URL + Request.PhotoInsertGet + '?userId=' + this.userObject.userID;
    this.uploader = new FileUploader({
      url: urlTest,
      authToken: 'Bearer ' + localStorage.getItem('token'),
      isHTML5: true,
      allowedFileType: ['image'],
      removeAfterUpload: true,
      autoUpload: false,
      maxFileSize: 10 * 1024 * 1024,

    });

    this.uploader.onAfterAddingFile = (item => {
      item.withCredentials = false;
    });
    this.uploader.onSuccessItem = (item, response, status, headers) => {
      if (response) {
        this.alertify.warning('Please wait for photo to upload!');
        const res: Photo = JSON.parse(response);
        const photo = {
          id: res.id,
          url: res.url,
          dateAdded: res.dateAdded,
          description: res.description,
          isMain: res.isMain,
        };
        if (this.userObject !== undefined) {
          this.userObject.photos.push(photo);
          if (photo.isMain) {
            this.authService.changeMemberPhoto(photo.url);
            this.authService.user.value.PhotoURL = photo.url;
            localStorage.setItem('userData', JSON.stringify(this.authService.user.value));
          }
        } else {
          this.vehicleObject.photos.push(photo);
          if (photo.isMain)
          {
            this.vehicleObject.vehiclePhotoUrl = photo.url;
          }
        }
      }
    };
    this.uploader.onErrorItem = (item, response, status, headers) => {
      if (response) {
        this.alertify.error(response);
      }
    };
  }

  setMainPhoto(photo: Photo)
  {
    if (this.vehicleObject !== undefined){
      this.requestService.setMainPhotoForVehicle(photo.id, this.vehicleObject.vehicleID).subscribe(() => {
        this.currentMain = this.vehicleObject.photos.filter(p => p.isMain === true)[0];
        this.currentMain.isMain = false;
        photo.isMain = true;
        this.getMemberPhotoChange.emit(photo.url);
      }, error => {
        this.alertify.error(error);
      });
    } else {
      this.requestService.setMainPhoto(photo.id, this.authService.decodedToken.nameid).subscribe(() => {
        this.currentMain = this.userObject.photos.filter(p => p.isMain === true)[0];
        this.currentMain.isMain = false;
        photo.isMain = true;
        this.getMemberPhotoChange.emit(photo.url);
        this.authService.changeMemberPhoto(photo.url);
        this.authService.user.value.PhotoURL = photo.url;
        localStorage.setItem('userData', JSON.stringify(this.authService.user.value));
      }, error => {
        this.alertify.error(error);
      });
    }
  }

  deletePhoto(id: number) {
    if (this.vehicleObject !== undefined){
      this.alertify.confirm('Are you sure you want to delete this photo?', () => {
        this.requestService.deletePhoto(id).subscribe(() => {
          this.vehicleObject.photos.splice(this.vehicleObject.photos.findIndex(p => p.id === id), 1);
          this.alertify.success('Photo has been deleted');
        }, error => {
          this.alertify.error('Failed to delete the photo');
        });
      });
    } else {
      this.alertify.confirm('Are you sure you want to delete this photo?', () => {
        this.requestService.deletePhoto(id).subscribe(() => {
          this.userObject.photos.splice(this.userObject.photos.findIndex(p => p.id === id), 1);
          this.alertify.success('Photo has been deleted');
        }, error => {
          this.alertify.error('Failed to delete the photo');
        });
      });
    }
  }
}
