import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {MatDrawer, MatSidenav} from '@angular/material/sidenav';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../_services/auth.service';
import { BreakpointObserver } from '@angular/cdk/layout';
import {RegisterDialogComponent} from './register/register-dialog/register-dialog.component';
import {MatDialog} from '@angular/material/dialog';
import {DrawerNavService} from '../_services/drawer-nav.service';
import {CityService} from '../_services/city.service';
import {User} from '../_models/user';

@Component({
  selector: 'app-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.css']
})
export class AuthComponent implements OnInit, AfterViewInit  {

  @ViewChild('drawer') public drawer: MatDrawer;
  isLoggedIn = false;
  isFullscreen = false;
  user = '';
  userImage = '';
  isSmallScreen: boolean;

  // tslint:disable-next-line:max-line-length
  constructor(private router: Router, private route: ActivatedRoute, private  auth: AuthService,
              private  breakpointObserver: BreakpointObserver,
              public dialog: MatDialog, private drawerService: DrawerNavService ) { }

  ngAfterViewInit(): void {
    this.drawerService.setDrawer(this.drawer);
    setTimeout(() => {
      if (this.route.snapshot.firstChild){
        this.drawerService.open();
      }else{
        this.drawerService.close();
      }}, 500);
  }

  ngOnInit(): void {
    this.auth.currentPhotoUrl.subscribe(photoUrl => this.userImage = photoUrl);
    this.breakpointObserver.observe('(max-width: 450px)').subscribe(obs => {
      this.isSmallScreen = obs.matches;
    });
    this.auth.user.subscribe(user => {
      if (user){
        this.isLoggedIn = true;
        this.user = user.Username;
        /*this.userImage = user.PhotoURL;*/
      }
    });
  }



  openDialog(): void {
    this.dialog.open(RegisterDialogComponent);
  }

  onBtnClickClose(): void {
    this.isFullscreen = false;
    this.drawer.opened = false;
  }

  onBtnFullscreen(): void {
    this.isFullscreen = true;

  }

  onBtnClick(): void {
    if (this.drawer.opened){
      return;
    }
    this.drawerService.toggle();
  }

  onLogout(): void {
    this.auth.logout();
    this.isLoggedIn = false;
    this.user = '';
  }
}
