import {AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {SessionUser} from '../../_services/session-user';
import {AuthService} from '../../_services/auth.service';
import {RequestService} from '../../_services/request.service';
import {Cargo, Chat, Profile, Reservations, Statistics, Vehicle} from "../../helpers/globals";
import { trigger, transition, animate, style } from '@angular/animations'
import {NotificationService} from "../../_services/notification.service";
import {DrawerNavService} from "../../_services/drawer-nav.service";
import {NotiService} from "../notification/notification.component";
import { MatDrawer } from '@angular/material/sidenav';
import {BreakpointObserver} from "@angular/cdk/layout";
import { Subject } from 'rxjs';
import {MatListOption} from "@angular/material/list";
declare var $:JQueryStatic;

interface NavItems {
  Name: string;
  Icon: string;
  Path: string;
  Style: string;
  Class?: string;
  Role: string;
  Hidden: boolean;
}
const navChildStyle = 'padding-left:15px; font-weight: 200';
const navParentStyle = 'font-weight: 500;';


@Component({
  selector: 'app-dashboard-sidenav',
  templateUrl: './dashboard-sidenav.component.html',
  styleUrls: ['./dashboard-sidenav.component.css'],
  animations: [
    trigger('slideInOut', [
      transition(':enter', [
        style({transform: 'translateY(-100%)'}),
        animate('200ms ease-in', style({transform: 'translateY(0%)'}))
      ]),
      transition(':leave', [
        animate('200ms ease-in', style({transform: 'translateY(-100%)'}))
      ])
    ])
  ]
})
export class DashboardSidenavComponent implements OnInit, AfterViewInit {
  @ViewChild('drawer') drawer: MatDrawer;
  isSmallScreen: any;
  isOpened = true;
  linkName: string;
  sessionUser: SessionUser;
  CarrierRole: boolean;
  ClientRole: boolean;
  HasOneUnopened: boolean = false;
  mode = '';

  typesOfNav: NavItems[] = [
    {Name: 'Home', Icon: 'home', Path: 'home', Style: navParentStyle,Class:'fas fa-chevron-circle-right', Role: '', Hidden: false},
    {Name: Vehicle, Icon: 'directions_car',Path: '', Style: navParentStyle, Class:'fas fa-chevron-circle-down', Role: 'Carrier', Hidden: false},
    {Name: 'My vehicles', Icon: 'garage', Path: 'vehicles', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: 'Add vehicle', Icon: 'add', Path: 'vehicles/addVehicle', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: Cargo, Icon: 'inventory', Path: '', Style: navParentStyle, Class:'fas fa-chevron-circle-down', Role: 'Client', Hidden: false},
    {Name: 'My cargos', Icon: 'view_in_ar', Path: 'cargos', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Client', Hidden: true},
    {Name: 'Add cargo', Icon: 'add', Path: 'cargos/addCargo', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Client', Hidden: true},
    {Name: Profile, Icon: 'account_box', Path: '', Style: navParentStyle, Class:'fas fa-chevron-circle-down', Role: '', Hidden: false},
    {Name: 'My profile', Icon: 'account_circle', Path: 'profile', Style: navChildStyle, Class:'fas fa-chevron-right', Role: '', Hidden: true},
    {Name: 'Change password', Icon: 'vpn_key', Path: 'changePassword', Style: navChildStyle, Class:'fas fa-chevron-right', Role: '', Hidden: true},
    {Name: Reservations, Icon: 'book_online', Path: '', Style: navParentStyle, Class:'fas fa-chevron-circle-down', Role: '', Hidden: false},
    {Name: 'View all', Icon: 'preview', Path: 'reservations', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: 'My freights', Icon: 'local_shipping', Path: 'freight/myFreights', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: 'Reviews', Icon: 'reviews', Path: 'freight/review', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: 'Freight report', Icon: 'summarize', Path: 'report', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: 'Management', Icon: 'manage_search', Path: 'reservations', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Client', Hidden: true},
    {Name: 'My reservations', Icon: 'list_alt', Path: 'reservations/myReservations', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Client', Hidden: true},
    {Name: 'Add reservation', Icon: 'add', Path: 'reservations/addReservation', Class:'fas fa-chevron-right', Style: navChildStyle, Role: 'Client', Hidden: true},
    {Name: Statistics, Icon: 'analytics', Path: '', Style: navParentStyle, Class:'fas fa-chevron-circle-down', Role: 'Carrier', Hidden: false},
    {Name: 'View statistic', Icon: 'insights', Path: 'statistics', Style: navChildStyle, Class:'fas fa-chevron-right', Role: 'Carrier', Hidden: true},
    {Name: Chat, Icon: 'chat', Path: '', Style: navParentStyle, Class:'fas fa-chevron-circle-down', Role: '', Hidden: false},
    {Name: 'Chat history', Icon: 'history', Path: 'chat', Style: navChildStyle, Class:'fas fa-chevron-right', Role: '', Hidden: true},
    {Name: 'LOGOUT', Icon: 'logout', Path: '/auth', Style: navParentStyle, Role: '', Hidden: false}
  ];
  typesOfNavFiltered: NavItems[];
  isNotificationOpen = false;

  constructor(private authService: AuthService, private notificationSignalService: NotificationService, private notiService: NotiService,
              private breakpointObserver: BreakpointObserver, private drawerService: DrawerNavService) { }

  ngOnInit(): void {
    this.linkName = 'Home';
    this.sessionUser = this.authService.user.value;
    this.assignRole();
    this.breakpointObserver.observe(['(max-width: 450px), (max-width: 650px)']).subscribe(obs => {

      console.log(obs);
      if (obs.breakpoints['(max-width: 450px)']){
        this.isSmallScreen = obs.breakpoints['(max-width: 450px)'];
        this.isOpened = false;

        this.mode = this.isSmallScreen ? 'over' : 'side';
      }else{
        this.isSmallScreen = obs.breakpoints['(max-width: 450px)'];
        this.isOpened = false;
        this.mode = this.isSmallScreen ? 'over' : 'side';
      }
      if (obs.breakpoints['(max-width: 650px)']){
        this.isOpened = false;
      }else {
        this.isOpened = true;
      }
    });
  }

  ngAfterViewInit() {
    $(document).ready(function(){
      $("#flip").click(function(){
        $("#panel").slideToggle("slow");
      });
    });
  }

  changeLinkName(name: string) {
    this.linkName = name;
    let updateItem;
    if (name === Vehicle && this.sessionUser.Role === 'Carrier'){
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'My vehicles');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Add vehicle');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
    if (name === Cargo && this.sessionUser.Role === 'Client'){
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'My cargos');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Add cargo');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
    if (name === Profile){
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'My profile');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Change password');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
    if (name === Reservations && this.sessionUser.Role === 'Carrier') {
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'View all');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'My freights');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Reviews');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Freight report');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
    if (name === Reservations && this.sessionUser.Role === 'Client'){
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Management');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'My reservations');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Add reservation');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
    if (name === Statistics){
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'View statistic');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
    if (name === Chat){
      updateItem = this.typesOfNavFiltered.find(element => element.Name === 'Chat history');
      updateItem.Hidden === true ? this.updateItemInArray(updateItem) : this.updateItemInArrayToTrue(updateItem);
    }
  }

  onClick() {
    this.drawer.toggle();
  }

  updateItemInArray(updateItem: any) {
    let index;
    updateItem.Hidden = false;
    index = this.typesOfNavFiltered.indexOf(updateItem);
    this.typesOfNavFiltered[index] = updateItem;
  }

  updateItemInArrayToTrue(updateItem: any) {
    let index;
    updateItem.Hidden = true;
    index = this.typesOfNavFiltered.indexOf(updateItem);
    this.typesOfNavFiltered[index] = updateItem;
  }

  assignRole(){
    this.authService.user.value.Role === 'Carrier' ? this.CarrierRole = true : this.ClientRole = true;
    if (this.ClientRole){
      const clientString = 'Client';
      this.typesOfNavFiltered = this.typesOfNav.filter((navItem: NavItems) => navItem.Role === clientString || navItem.Role === '');
    }else{
      const carrierString = 'Carrier';
      this.typesOfNavFiltered = this.typesOfNav.filter((navItem: NavItems) => navItem.Role === carrierString || navItem.Role === '');
    }
  }

  onNotificationSwitch(): void {
    if (!this.isNotificationOpen){
      this.HasOneUnopened = false;
      this.notificationSignalService.unopenedNotifications = 0;
      for(let i = 0; i < this.notiService.data.value.length; i++){
        if(this.notiService.data.value[i].open === false){
          this.notiService.data.value[i].open = true;
          this.HasOneUnopened = true;
        }
      }
      this.HasOneUnopened === true? this.notificationSignalService.DeselectNotifications() : this.HasOneUnopened = false;
    }
    this.isNotificationOpen = !this.isNotificationOpen;
  }

  getLocation(): void{
    navigator.geolocation.getCurrentPosition((position)=> {
      const longitude = position.coords.longitude;
      const latitude = position.coords.latitude;
      console.log(longitude, latitude);
    });
   /* if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position)=>{
        const longitude = position.coords.longitude;
        const latitude = position.coords.latitude;
        console.log(longitude, latitude);
      });
    } else {
      console.log("No support for geolocation")
    }*/
  }

}
