import { Injectable } from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {BehaviorSubject, Observable} from 'rxjs';
import {Request} from '../helpers/request.enum';
import {City} from '../_models/city';
import {SessionUser} from './session-user';
import {citySearchObject} from '../_models/searchObjects/citySearchObject';
import {map, tap} from 'rxjs/operators';
import {User} from '../_models/user';

@Injectable({
  providedIn: 'root'
})
export class CityService {

  constructor(private http: HttpClient) { }

  getSelectedCities(id: number) {
    return this.http.get<City[]>(Request.URL + Request.CityInsertGet + '?countryID=' + id);
  }
}
