import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {VehicleModel} from '../../../_models/vehicle-model';
import {VehicleType} from '../../../_models/vehicle-type';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {VehicleDetails} from '../../../_models/vehicle-details';
import {RequestService} from '../../../_services/request.service';
import {AlertifyService} from '../../../_services/alertify.service';
import {VehicleInsertRequest} from '../../../_models/requests/vehicleInsertRequest';
import {Router} from '@angular/router';
import {Request} from "../../../helpers/request.enum";

@Component({
  selector: 'app-vehicles-add',
  templateUrl: './vehicles-add.component.html',
  styleUrls: ['./vehicles-add.component.css']
})
export class VehiclesAddComponent implements OnInit {
  addVehicleForm: FormGroup;
  vehicleModels: VehicleModel[];
  vehicleTypes: VehicleType[];
  vehicle: VehicleInsertRequest = new VehicleInsertRequest();
  vehicleDetails: VehicleDetails = {};
  isHidden = false;
  description: string;

  constructor(public dialog: MatDialog, private formBuilder: FormBuilder,
              private requestService: RequestService<any>, private alertify: AlertifyService,
              private router: Router) { }

  ngOnInit(): void {
    this.requestService.get(Request.VehicleModelInsertGet).subscribe(result => {
      this.vehicleModels = result;
    });
    this.requestService.get(Request.VehicleTypeInsertGet).subscribe(result => {
      this.vehicleTypes = result;
    });
    this.createAddForm();
  }

  createAddForm() {
    this.addVehicleForm = this.formBuilder.group({
      licencePlate: ['', Validators.required],
      seatingCapacity: [0, Validators.required],
      trunkVolume: [0, Validators.required],
      yearOfManufacture: [null, Validators.required],
      vehicleModelID: ['', Validators.required],
      vehicleTypeID: ['', Validators.required]
    });
  }

  add(): void {
    this.vehicle = Object.assign({}, this.addVehicleForm.value);
    this.vehicle.vehicleDetails = this.vehicleDetails != null && this.vehicleDetails.maxHeight != undefined? this.vehicleDetails : null;
    this.requestService.insert(this.vehicle, Request.VehicleInsertInsertGet).subscribe(() => {
      this.alertify.success('Vehicle added successfully!');
      }, error => {
        this.alertify.error(error);
      }, () => {
      this.router.navigate(['/dashboard/vehicles']);
    });
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(VehiclesAddDialog, {
      width: '600px',
      height: '400px',
      data: {vehicleDetails: this.vehicleDetails}
    })

    dialogRef.afterClosed().subscribe(result => {
      this.vehicleDetails = result;
      this.vehicleDetails !== undefined ? this.isHidden = true : this.isHidden = false;
    });
  }

  deleteDetails() {
    this.isHidden = false;
    this.vehicleDetails = null;
  }


}

@Component({
  selector: 'app-vehicles-add-dialog',
  templateUrl: './vehicles-add-dialog.component.html',
})
// tslint:disable-next-line:component-class-suffix
export class VehiclesAddDialog {

  constructor(
    public dialogRef: MatDialogRef<VehiclesAddDialog>,
    @Inject(MAT_DIALOG_DATA) public data: VehicleDetails) {
  }

  public onNoClick(): void {
    this.dialogRef.close();
  }


}
