export class SessionUser{
  Role: string;
  Email: string;
  Username: string;
  PhotoURL: string;
  Rating?: number | null;
  private readonly _JWT: string;
  // tslint:disable-next-line:variable-name
  private readonly _tokenExpirationDate: Date;
  // tslint:disable-next-line:max-line-length
  constructor(Role: string, Email: string, Username: string, JWT: string, tokenExpirationDate: Date, PhotoURL: string, Rating: number) {
    this.Role = Role;
    this.Email = Email;
    this.Username = Username;
    this._JWT = JWT;
    this._tokenExpirationDate = tokenExpirationDate;
    this.PhotoURL = PhotoURL;
    this.Rating = Rating;
  }

  get ExpDate(): Date{
    return this._tokenExpirationDate;
  }
  get Token(): string{
    if (!this._tokenExpirationDate || new Date() > this._tokenExpirationDate) {
      return null;
    }
    return this._JWT;
  }
}
