import { Component, OnInit, Input } from '@angular/core';
import {CargoReservation} from '../../../_models/cargo-reservation';
import {Carrier} from '../../../_models/carrier';
import {SessionUser} from '../../../_services/session-user';
import {AuthService} from '../../../_services/auth.service';
import {RequestService} from '../../../_services/request.service';
import {AlertifyService} from '../../../_services/alertify.service';
import {User} from '../../../_models/user';
import {RatingCarrier} from '../../../_models/rating-carrier';

@Component({
  selector: 'app-reservations-card',
  templateUrl: './reservations-card.component.html',
  styleUrls: ['./reservations-card.component.css']
})
export class ReservationsCardComponent implements OnInit {
  @Input() reservation: CargoReservation;
  @Input() userCarrier: User;
  sessionUser: SessionUser;
  carrierPhotoUrl: string;
  reservationPhotoUrl: string;
  rating: number;

  constructor(private authService: AuthService, private requestService: RequestService<any>, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    if (this.userCarrier != undefined){
      this.carrierPhotoUrl = this.userCarrier.photoURL;
      this.rating = this.userCarrier.carrier.rating;
    }
    if (this.reservation != undefined){
      this.reservationPhotoUrl = this.reservation.client.user.photoURL;
    }
  }

}
