import { Component, OnInit } from '@angular/core';
import {AuthService} from "../../../_services/auth.service";
import {RequestService} from "../../../_services/request.service";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "../../../_services/alertify.service";
import {SessionUser} from "../../../_services/session-user";
import {CargoReservation} from "../../../_models/cargo-reservation";
import {NgxGalleryAnimation, NgxGalleryImage, NgxGalleryOptions} from "ngx-gallery-9";
import {serverPicturePath} from "../../../helpers/globals";
import {CommonService} from "../../../_services/common.service";
import {StatusEnum} from "../../../helpers/Status.Enum";

@Component({
  selector: 'app-reservation-details',
  templateUrl: './reservation-details.component.html',
  styleUrls: ['./reservation-details.component.css']
})
export class ReservationDetailsComponent implements OnInit {
  sessionUser: SessionUser;
  cargoReservation: CargoReservation;
  galleryOptions: NgxGalleryOptions[];
  galleryImages: NgxGalleryImage[];
  IsHidden: boolean;
  IsSpecificCarrier: boolean;



  constructor(private authService: AuthService, private reqService: RequestService<any>, private route: ActivatedRoute,
              private alertify: AlertifyService, private commonService: CommonService,private router: Router) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.route.data.subscribe(data => {
      this.cargoReservation = data.cargoReservations;
    });
    this.cargoReservation.statusStr === StatusEnum.NotAccepted && this.cargoReservation.freightID === null ? this.IsHidden = true : this.IsHidden = false;
    this.cargoReservation.freight != null && (this.cargoReservation.freight.statusStr === StatusEnum.Initial || this.cargoReservation.freight.statusStr === StatusEnum.Rejected) ? this.IsSpecificCarrier = true : this.IsSpecificCarrier = false;
    this.galleryOptions = [
      {
        width: '500px',
        height: '500px',
        imagePercent: 100,
        thumbnailsColumns: 4,
        imageAnimation: NgxGalleryAnimation.Slide,
        preview: false
      }
    ];
    this.galleryImages = this.getImages();
  }
  getImages() {
    const imageUrls = [];
    for (const photos of this.cargoReservation.cargo.cargoPhotos) {
      imageUrls.push({
        small: serverPicturePath + photos.url,
        medium: serverPicturePath + photos.url,
        big: serverPicturePath + photos.url,
        description: photos.description
      });
    }
    return imageUrls;
  }
  public createImgPath = (serverPath: string) => {
    return `http://localhost:52391${serverPath}`;
  }

  open(cargoReservation: CargoReservation) {
    this.commonService.updateStatusRedirect(cargoReservation);
  }
}
