import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {Vehicle} from "../../../_models/vehicle";
import {Photo} from "../../../_models/photo";
import {VehicleModel} from "../../../_models/vehicle-model";
import {VehicleType} from "../../../_models/vehicle-type";
import {AlertifyService} from "../../../_services/alertify.service";
import {ActivatedRoute, Router} from "@angular/router";
import {RequestService} from "../../../_services/request.service";
import {Request} from "../../../helpers/request.enum";
import {Cargo} from "../../../_models/cargo";
import {NgxGalleryAnimation, NgxGalleryImage, NgxGalleryOptions} from "ngx-gallery-9";
import {serverPicturePath} from "../../../helpers/globals";

@Component({
  selector: 'app-cargo-detail',
  templateUrl: './cargo-detail.component.html',
  styleUrls: ['./cargo-detail.component.css']
})
export class CargoDetailComponent implements OnInit {
  updateCargoForm: FormGroup;
  addMoreImagesForm: FormGroup;
  cargo: Cargo;
  galleryOptions: NgxGalleryOptions[];
  galleryImages: NgxGalleryImage[];
  cargoValidation: Cargo;
  cargoPhotoUrl: string;
  formData: FormData;

  constructor(private alertify: AlertifyService, private route: ActivatedRoute, private requestService: RequestService<any>,
              private formBuilder: FormBuilder, private router: Router) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.cargo = data.cargoDetail;
    });
    this.formData = new FormData()
    this.cargoPhotoUrl = serverPicturePath + this.cargo.photoUrl;
    this.galleryOptions = [
      {
        width: '500px',
        height: '500px',
        imagePercent: 100,
        thumbnailsColumns: 4,
        imageAnimation: NgxGalleryAnimation.Slide,
        preview: false
      }
    ];
    this.galleryImages = this.getImages();
    this.createUpdateForm();
    this.createAddImageForm();
    this.cargoValidation = Object.assign({}, this.updateCargoForm.value);
  }

  getImages() {
    const imageUrls = [];
    for (const photos of this.cargo.cargoPhotos) {
      imageUrls.push({
        small: serverPicturePath + photos.url,
        medium: serverPicturePath + photos.url,
        big: serverPicturePath + photos.url,
        description: photos.description
      });
    }
    return imageUrls;
  }

  update() {
    this.cargo = Object.assign({}, this.updateCargoForm.value);
    if (JSON.stringify(this.cargoValidation) === JSON.stringify(this.cargo))
    {
      this.alertify.error('The values are the same as the old ones! (Umbasa)');
    } else {
      this.updateCargoForm.reset(this.cargo);
      this.cargoValidation = this.cargo;
      this.requestService.update(this.cargo.cargoID, this.cargo, Request.CargoUpdateGetID).subscribe(next => {
        this.alertify.success('Cargo updated successfully!');
      }, error => {
        this.alertify.error(error);
      });
    }
    this.updateCargoForm.reset(this.cargo);
  }

  createUpdateForm() {
    this.updateCargoForm = this.formBuilder.group({
      name: [this.cargo.name, Validators.required],
      maxHeight: [this.cargo.maxHeight, Validators.required],
      maxWidth: [this.cargo.maxWidth, Validators.required],
      weight: [this.cargo.weight, Validators.required],
      description: [this.cargo.description, Validators.required]
    });
  }

  createAddImageForm() {
    this.addMoreImagesForm = this.formBuilder.group({
      images: ['', Validators.required]
    });
  }

  public onImageClick(event: any) {
    if (event.target.files.length > 0) {
      this.formData.delete('images');
      /*let files = event.target.files;*/
      /*this.formData.append('images', files);*/
      let filesToUpload : File[] = event.target.files;
      Array.from(filesToUpload).map((file, index) => {
        this.formData.append('images', file, file.name);
      });
    }
  }

  addImages() {
    this.requestService.update(this.cargo.cargoID,this.formData, Request.CargoUPDATEIMAGES).subscribe(() => {
      this.alertify.success('Cargo images added successfully!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.router.navigate(['/dashboard/cargos']);
    });
  }
}
