import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router} from '@angular/router';
import {AlertifyService} from '../_services/alertify.service';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {AuthService} from '../_services/auth.service';
import {RequestService} from '../_services/request.service';
import {Country} from '../_models/country';
import {Request} from "../helpers/request.enum";
import {City} from "../_models/city";


@Injectable()
export class CityResolver implements Resolve<City[]> {
  constructor(private requestService: RequestService<any>,
              private authService: AuthService,
              private router: Router, private alertify: AlertifyService){}

  resolve(route: ActivatedRouteSnapshot): Observable<City[]> {
    return this.requestService.get(Request.CityInsertGet).pipe(
      catchError(error => {
        this.alertify.error('Problem retrieving your data');
        this.router.navigate(['/home']);
        return of(null);
      })
    );
  }

}
