import { Injectable } from '@angular/core';
import {HubConnection, HubConnectionBuilder} from "@microsoft/signalr";
import {SignalMessage} from "../_models/signalR/signal-message";
import {AuthService} from "./auth.service";
import {SessionUser} from "./session-user";
import {Notification} from "../_models/notification";
import { NotiService } from '../dashboard/notification/notification.component';
import {BehaviorSubject, Subject} from "rxjs";
import {ChatMessage} from "../_models/signalR/chat-message";

@Injectable({
  providedIn: 'root'
})
export class NotificationService {
  private connection: HubConnection;
  unopenedNotifications: number;
  sessionUser: SessionUser;



  constructor(private authService: AuthService, private notiService: NotiService) { }

  public async StopConnection() {
    try {
      await this.connection.stop();
      console.log("Connection stopped");
    }
    catch (err){
      console.log(err);
    }
  }

  public async StartConnection() {
    try {
      await this.connection.start();
      this.connection.invoke("GetNotifications", 10).catch(err => console.error(err));
      console.log("Connection started");
    }
    catch (err){
      console.log(err);
    }
  }

  InitializeNotificationConnection() {
    this.sessionUser = this.authService.user.value;
    this.connection = new HubConnectionBuilder().withUrl("http://localhost:52391/notification", {
      accessTokenFactory: () => this.sessionUser.Token,
    }).withAutomaticReconnect().build();

    this.StartConnection().then(value => console.log(value));

    this.connection.on("GetNotification", (res: Notification) => {
      this.unopenedNotifications = this.unopenedNotifications + 1;
      this.notiService.emitter.next(this.unopenedNotifications);
      this.notiService.data.value.unshift(res);
      //this.notiService.item.next(res);
    });

    this.connection.on("GetAllNotifications", (res: Notification[]) => {
      this.unopenedNotifications = res.filter((notification: Notification) => notification.open === false).length;
      this.notiService.emitter.next(this.unopenedNotifications);
      this.notiService.data.next(res);
    });
  }

  DeselectNotifications() {
    this.connection.invoke("DeselectNotification", this.notiService.data.value).catch(err => console.error(err));
  }

  Destroy() {
    this.notiService.emitter.unsubscribe();
    this.notiService.data.unsubscribe();
    //this.notiService.item.unsubscribe();

    this.notiService.emitter = new Subject<number>();
    this.notiService.data = new BehaviorSubject<Notification[]>(null);
    //this.notiService.item = new BehaviorSubject<Notification>(null);
  }

}
