import { Injectable } from '@angular/core';
import {StatusEnum} from "../helpers/Status.Enum";
import {Freight} from "../_models/freight";
import {ChartDataSets, ChartOptions, ChartType} from "chart.js";
import {Label, SingleDataSet} from "ng2-charts";
import * as pluginDataLabels from "chartjs-plugin-datalabels";

@Injectable({
  providedIn: 'root'
})
export class StatisticsService {
  public vehicleArray: Freight[] = [];
  public totalEarnedPrice: number = 0;
  public totalSpentPrice: number = 0;
  public totalFreights: number = 0;
  public totalDistance: number = 0;
  public IsUniqueVehicleID: boolean = false;
  public numberOfOccurrences: number[] = [];
  public totalFinishedReservation: number = 0;
  public totalNotAcceptedPrice: number = 0;
  //------------------------------------------------------------------------------------
  //pie chart data
  public pieChartOptions: ChartOptions = {
    responsive: true,
  };
  public pieChartLabels: Label[] = [];
  public pieChartData: SingleDataSet = [];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [pluginDataLabels];
  public pieChartColors = [
    {
      backgroundColor: ['rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)', 'rgba(0,0,255,0.3)'],
    },
  ];
  //------------------------------------------------------------------------------------
  //bar chart data
  public barChartOptions: ChartOptions = {
    responsive: true,
    // We use these empty structures as placeholders for dynamic theming.
    scales: { xAxes: [{}], yAxes: [{}] },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  public barChartLabels: Label[] = ['January', 'February', 'March', 'April','May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartData: ChartDataSets[] = [];


  constructor() { }


  public doStatistic(paramFreight: any) {
    this.totalEarnedPrice = 0;
    this.totalDistance = 0;
    this.numberOfOccurrences = [];
    let finishedFreights = 0;
    for (let item of paramFreight){
      if (item.statusStr != StatusEnum.Rejected){
        if (item.statusStr === StatusEnum.Finished || item.statusStr === StatusEnum.Rated){
          this.totalEarnedPrice += item.price;
          this.totalDistance += item.distance;
          finishedFreights++;
        }
        this.vehicleArray.length === 0 ? this.vehicleArray.push(item) : null;
        let test = this.vehicleArray.find(x => x.vehicleID === item.vehicleID);
        test === undefined ? this.IsUniqueVehicleID = true : this.IsUniqueVehicleID = false;
        if(this.IsUniqueVehicleID) {
          this.vehicleArray.push(item);
        }
        this.IsUniqueVehicleID = false;
      }
    }
    this.totalFreights = finishedFreights;

    for (let vehicle of this.vehicleArray) {
      let count = paramFreight.filter(c => c.vehicleID === vehicle.vehicleID && c.statusStr != StatusEnum.Rejected).length;
      let percentage = (count / paramFreight.filter(f => f.statusStr != StatusEnum.Rejected).length) * 100;
      this.numberOfOccurrences.push(Math.round(percentage));
    }
    this.loadPieChartData();
  }

  loadPieChartData() {
    this.pieChartLabels = [];
    for (let vehicle of this.vehicleArray) {
      this.pieChartLabels.push(vehicle.vehicle?.vehicleModel + ' %');
    }
    this.pieChartData = this.numberOfOccurrences;
  }

  public doClientStatistic(paramCargo: any){
    let finishedReservation = 0;
    let notAcceptedPrice = 0;
    for (let item of paramCargo){
      if (item.statusStr === StatusEnum.Finished || item.statusStr === StatusEnum.Payed || item.statusStr === StatusEnum.Rated){
        finishedReservation++
      }
      if (item.statusStr === StatusEnum.Payed || item.statusStr === StatusEnum.Rated) {
        this.totalSpentPrice += item.freight?.price;
      }
      if (item.freight != null && item.freight.statusStr === 'NotAccepted') {
        notAcceptedPrice++;
      }
    }
    this.totalFinishedReservation = finishedReservation;
    this.totalNotAcceptedPrice = notAcceptedPrice;
  }

  loadBarChartData(param: any) {

    this.barChartData = [
      { data: param, label: 'Total freights by month' }
    ]
  }

  resetData() {
  this.vehicleArray = [];
  this.totalEarnedPrice = 0;
  this.totalSpentPrice= 0;
  this.totalFreights= 0;
  this.totalDistance = 0;
    this.IsUniqueVehicleID = false;
    this.numberOfOccurrences = [];
    this.totalFinishedReservation = 0;
    this.totalNotAcceptedPrice = 0;
    this.barChartData = [];
    this.pieChartLabels = [];
    this.pieChartData = [];
  }

  assignDefaultValues() {
    this.pieChartData = [];
    this.pieChartLabels = [];
    this.barChartData = [];
  }
}
