import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {Vehicle} from '../../_models/vehicle';
import {SessionUser} from '../../_services/session-user';
import {AuthService} from '../../_services/auth.service';
import {ActivatedRoute} from '@angular/router';
import {PaginatedResult, Pagination} from '../../_models/pagination';
import {RequestService} from '../../_services/request.service';
import {AlertifyService} from '../../_services/alertify.service';
import {Request} from "../../helpers/request.enum";
import {forItemPerPage, forPageMax, forPageStart, pageNumber, pageSize} from "../../helpers/globals";
import {MatDialog} from "@angular/material/dialog";
import {DrawerNavService} from "../../_services/drawer-nav.service";
import {MatDrawer} from "@angular/material/sidenav";

@Component({
  selector: 'app-vehicles',
  templateUrl: './vehicles.component.html',
  styleUrls: ['./vehicles.component.css']
})
export class VehiclesComponent implements OnInit, AfterViewInit {

  vehicles: Vehicle[];
  vehicleParams: any = {};
  pagination: Pagination;
  sessionUser: SessionUser;
  IsVehicleUndefined: boolean;
  paginationAvailable: boolean;
  isSmallScreen: boolean;
  isFullscreen = false;
  HiddenBtn: boolean;
  @ViewChild('drawer') public drawer: MatDrawer;


  constructor(private authService: AuthService, private route: ActivatedRoute,
              private requestService: RequestService<any>, private alertify: AlertifyService,public dialog: MatDialog, private drawerService: DrawerNavService ) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.setVehicleParams();
    this.requestService.getPaginatedResult(pageNumber,pageSize,Request.VehicleGETPAGE).subscribe( (res: PaginatedResult<Vehicle[]>)=> {
      this.vehicles = res.result;
      this.pagination = res.pagination;
    },() => {
    /*  this.vehicles === undefined || this.vehicles.length === 0 ? this.IsVehicleUndefined = true: this.IsVehicleUndefined = false;
      this.pagination === undefined ? this.paginationAvailable = false: this.paginationAvailable = true;*/
    }, () => {
      this.vehicles === undefined || this.vehicles.length === 0 ? this.IsVehicleUndefined = true: this.IsVehicleUndefined = false;
      this.pagination === undefined ? this.paginationAvailable = false: this.paginationAvailable = true;
    });
  }

  setVehicleParams() {
    this.vehicleParams.name = '';
    this.vehicleParams.type = '';
    this.vehicleParams.orderBy = 'all';
    this.vehicleParams.isFavourite = false;
  }

  ngAfterViewInit() {
    this.drawerService.setDrawer(this.drawer);
    setTimeout(() => {
      if (this.route.snapshot.firstChild){
        this.drawerService.open();
      }else{
        this.drawerService.close();
      }}, 500);
  }

  pageChanged(event: any):void {
    this.pagination.currentPage = event.page
    this.loadVehicles();
  }

  loadVehicles(){
    this.requestService.getPaginatedResult(this.pagination.currentPage, this.pagination.itemsPerPage,Request.VehicleGETPAGE, this.vehicleParams).subscribe((res: PaginatedResult<Vehicle[]>) => {
      this.vehicles = res.result;
      this.pagination = res.pagination;
    }, error => {
      this.alertify.error(error);
    });
  }

  resetFilters() {
    this.vehicleParams.name = '';
    this.vehicleParams.type = '';
    this.vehicleParams.isFavourite = 'false';
    this.loadVehicles();
  }

  deleteFromList(id: number) {
      for (let i = forPageStart; i <= forPageMax; i+=forItemPerPage){
          if (this.pagination.totalItems === i)
            this.pagination.currentPage -=1;
       }
    this.vehicles.splice(this.vehicles.findIndex(p => p.vehicleID === id), 1);
    this.vehicles.length === 0 ? this.paginationAvailable = false : this.paginationAvailable = true;
  }

  //Drawer
  onBtnClickClose(): void {
    this.HiddenBtn = false;
    this.isFullscreen = false;
    this.drawer.opened = false;
  }
  onBtnFullscreen(): void {
    this.isFullscreen = true;

  }
  onBtnClick(): void {
    this.HiddenBtn = true;
    if (this.drawer.opened){
      return;
    }
    this.drawerService.toggle();
  }

}
