import {Injectable} from '@angular/core';
import {User} from '../_models/user';
import {ActivatedRouteSnapshot, Resolve, Router} from '@angular/router';
import {AlertifyService} from '../_services/alertify.service';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {AuthService} from '../_services/auth.service';
import {RequestService} from '../_services/request.service';
import { Request } from "../helpers/request.enum";
import {Cargo} from "../_models/cargo";


@Injectable()
export class CargoResolver implements Resolve<Cargo[]> {
  constructor(private requestService: RequestService<any>,
              private authService: AuthService,
              private router: Router, private alertify: AlertifyService){}

  resolve(route: ActivatedRouteSnapshot): Observable<Cargo[]> {
    return this.requestService.get(Request.CargoGETNOPAGING).pipe(
      catchError(error => {
        this.alertify.error('Problem retrieving your data');
        this.router.navigate(['/home']);
        return of(null);
      })
    );
  }

}
