import {Component, OnInit, ViewChild, AfterViewInit, OnDestroy} from '@angular/core';
import {AbstractControl, FormBuilder, FormControl, FormGroup, FormGroupDirective,  NgForm, Validators} from '@angular/forms';
import { AuthService } from 'src/app/_services/auth.service';
import {AlertifyService} from '../../../_services/alertify.service';
import {Router} from '@angular/router';
import {ApplicationUserCreateRequest} from '../../../_models/requests/applicationUserCreateRequest';
import {CountryService} from '../../../_services/country.service';
import {Country} from '../../../_models/country';
import {CityService} from '../../../_services/city.service';
import {City} from '../../../_models/city';
import {ReplaySubject, Subject} from 'rxjs';
import {takeUntil, take} from 'rxjs/operators';
import {MatSelect} from '@angular/material/select';
import {ErrorStateMatcher} from '@angular/material/core';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    return control.parent.errors && control.parent.errors.mismatch && control.parent.touched;
  }
}

@Component({
  selector: 'app-register-client',
  templateUrl: './register-client.component.html',
  styleUrls: ['./register-client.component.css']
})
export class RegisterClientComponent implements OnInit, OnDestroy {
  registerClientForm: FormGroup;
  user: ApplicationUserCreateRequest = new ApplicationUserCreateRequest();
  countries: Country[];
  cities: City[];
  matcher = new MyErrorStateMatcher();

  public citiesFilterCtrl: FormControl = new FormControl();
  public citiesCtrl: FormControl = new FormControl();
  public filteredCities: ReplaySubject<City[]> = new ReplaySubject<City[]>(1);
  // tslint:disable-next-line:variable-name
  protected _onDestroy = new Subject<void>();
  @ViewChild('singleSelect', { static: true }) singleSelect: MatSelect;


  constructor(private authService: AuthService, private alertify: AlertifyService, private formBuilder: FormBuilder,
              private router: Router, private countryService: CountryService, private cityService: CityService) { }

  ngOnInit(): void {
    this.createRegisterClientForm();
    this.getAllCountries();

    this.citiesFilterCtrl.valueChanges
      .pipe(takeUntil(this._onDestroy))
      .subscribe(() => {
        this.filterCities();
      });
  }

  // tslint:disable-next-line:typedef
  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();
  }

  // tslint:disable-next-line:typedef
  protected filterCities() {
    if (!this.cities) {
      return;
    }
    // get the search keyword
    let search = this.citiesFilterCtrl.value;
    if (!search) {
      this.filteredCities.next(this.cities.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    this.filteredCities.next(
      this.cities.filter(city => city.name.toLowerCase().indexOf(search) > -1)
    );
  }

  // tslint:disable-next-line:typedef
  createRegisterClientForm() {
    this.registerClientForm = this.formBuilder.group({
      client: this.formBuilder.group({
        firstName: ['', Validators.required],
        lastName: ['', Validators.required],
        dateOfBirth: [null, Validators.required],
        gender: ['male'],
      }),
      address: this.formBuilder.group({
        name: ['', Validators.required],
        cityID: ['', Validators.required]
      }),
      username: ['', Validators.required],
      email: ['', [Validators.email, Validators.required]],
      password: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(10)]],
      confirmPassword: [''],
    }, {validator: this.passwordMatchValidator});
  }

  // tslint:disable-next-line:typedef
  passwordMatchValidator(g: FormGroup) {
    return g.get('password').value === g.get('confirmPassword').value ? null : {mismatch: true};
  }

  register(): void {
    if (this.registerClientForm.valid) {
      this.user = Object.assign({}, this.registerClientForm.value);
      this.authService.register(this.user).subscribe(() => {
        this.alertify.success('Registration successful');
      }, error => {
        this.alertify.error(error);
      }, () => {
        this.router.navigate(['auth/login']);
      });
    }
  }

  getAllCountries(): void {
    this.countryService.getCountries().subscribe(res => {
      this.countries = res;
    }, error => {
      if (error.message === undefined)
      {
        this.alertify.error('API not started');
      }else{
        this.alertify.error(error);
      }
    });
  }

  assignCity(city: City, event: any): void {
    if (event.isUserInput){
      this.registerClientForm.controls.address.patchValue({
        cityID: city.cityID
      });
    }
  }

  loadCities(id: number, event: any): void {
    this.cityService.getSelectedCities(id).subscribe(res => {
      this.cities = res;
      this.citiesCtrl.setValue(this.cities);
      this.filteredCities.next(this.cities.slice());
    });
    this.filteredCities
      .pipe(take(1), takeUntil(this._onDestroy))
      .subscribe(() => {
        this.singleSelect.compareWith = (a: City, b: City) => a && b && a.cityID === b.cityID;
      });
  }
}
