import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {RequestService} from "../../../_services/request.service";
import { AuthService } from 'src/app/_services/auth.service';
import {SessionUser} from "../../../_services/session-user";
import {Request} from "../../../helpers/request.enum";
import {ActivatedRoute, Router} from "@angular/router";
import {User} from "../../../_models/user";
import {AlertifyService} from "../../../_services/alertify.service";

@Component({
  selector: 'app-user-settings',
  templateUrl: './user-settings.component.html',
  styleUrls: ['./user-settings.component.css']
})
export class UserSettingsComponent implements OnInit {
  userSettingsForm: FormGroup;
  user: User;

  constructor(private formBuilder: FormBuilder, private requestService: RequestService<any>, private authService: AuthService,
              private  route: ActivatedRoute, private alertify: AlertifyService, private router: Router) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.user = data.userLog;
    });
    this.createUpdateSettingsForm();
  }

  createUpdateSettingsForm() {
    this.userSettingsForm = this.formBuilder.group({
      currentPassword: ['', Validators.required],
      password: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(30)]],
      confirmPassword: [''],
    }, {validator: this.passwordMatchValidator});
  }

  // tslint:disable-next-line:typedef
  passwordMatchValidator(g: FormGroup) {
    return g.get('password').value === g.get('confirmPassword').value ? null : {'mismatch': true};
  }

  update() {
    var currentPassword = this.userSettingsForm.get('currentPassword').value;
    if (currentPassword != this.authService.password.value) {
      this.alertify.error("You're password is not the same as the current one!");
      return;
      this.userSettingsForm.reset();
    }
    const formData: FormData = new FormData();
    formData.append('Password', this.userSettingsForm.get('password').value);
    formData.append('CurrentPassword', this.userSettingsForm.get('currentPassword').value);
    this.requestService.update(this.user.userID, formData, Request.UserUPDATEPASSWORD).subscribe(next => {
      this.alertify.success('Password updated successfully!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.authService.logout();
    });
    this.userSettingsForm.reset();
  }
}
