import { Component, OnInit } from '@angular/core';
import {CargoReservation} from "../../../_models/cargo-reservation";
import {AuthService} from "../../../_services/auth.service";
import {RequestService} from "../../../_services/request.service";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "../../../_services/alertify.service";
import {CommonService} from "../../../_services/common.service";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {Request} from "../../../helpers/request.enum";
import {PaymentInsertRequest} from "../../../_models/requests/paymentInsertRequest";
import { FreightInsertRequest } from 'src/app/_models/requests/freightInsertRequest';
import {CargoReservationInsertRequest} from "../../../_models/requests/cargo-reservation-insert-request";
import {StatusEnum} from "../../../helpers/Status.Enum";

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit {
  cargoReservation: CargoReservation;
  rating: number;
  paymentForm: FormGroup;
  paymentInsertRequest: PaymentInsertRequest = new PaymentInsertRequest();
  freightInsertRequest: FreightInsertRequest = new FreightInsertRequest();
  cargoReservationInsertRequest: CargoReservationInsertRequest = new CargoReservationInsertRequest();

  ShowSuccess: boolean;
  currentDate = new Date();

  constructor(private authService: AuthService, private requestService: RequestService<any>, private route: ActivatedRoute,
              private alertify: AlertifyService, private commonService: CommonService,private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.cargoReservation = data.cargoReservations;
    });
    this.rating = this.cargoReservation.freight.carrier.rating;
    this.createPaymentForm();
  }

  createPaymentForm() {
    this.paymentForm = this.formBuilder.group({
      cardName: ['', Validators.required],
      cardNumber: ['', Validators.required],
      cardExpMonth: ['', Validators.required],
      cardExpYear: ['', Validators.required],
      cardCVC: ['', Validators.required]
    });
  }

  pay() {
    this.paymentInsertRequest = Object.assign({}, this.paymentForm.value);
    this.paymentInsertRequest.amount = this.cargoReservation.freight.price;
    this.freightInsertRequest.statusStr = StatusEnum.Payed;
    this.cargoReservationInsertRequest.statusStr = StatusEnum.Payed;
    this.requestService.insert(this.paymentInsertRequest, Request.PaymentInsertGet).subscribe(() => {
      this.alertify.success('Payment successful!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.ShowSuccess = true;
    });
    this.requestService.update(this.cargoReservation.freightID, this.freightInsertRequest, Request.FreightUpdateGet).subscribe();
    this.requestService.update(this.cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID).subscribe();
  }

}
