import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../_services/auth.service';
import {Router} from '@angular/router';
import {NgForm} from '@angular/forms';
import {map, take} from 'rxjs/operators';
import {AlertifyService} from '../../_services/alertify.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private authService: AuthService, private router: Router, private alertifyService: AlertifyService) { }

  isLogingIn = false;
  ngOnInit(): void {
    this.authService.user.pipe(
      take(1),
      map(user => {
        const isAuth = !!user;
        if (isAuth) {
          if (this.authService.user){
            this.isLogingIn = false;
            setTimeout(() => {
              this.isLogingIn = false;
              this.router.navigate(['/dashboard']);
            }, 2500);
          }
        }
      })).subscribe(r => {
    });
  }

  onSubmit(authF: NgForm): void {
    if (!authF.valid) {
      return;
    }
    const email = authF.control.get(['email']).value;
    const pass = authF.control.get(['password']).value;
    const rememberMe = authF.control.get(['remember']).value;
    this.isLogingIn = true;
    this.authService.login(email, pass, rememberMe).subscribe(res => {
      this.alertifyService.success('Logged in successfully');
      this.isLogingIn = false;
    }, error => {
      if (error.message === undefined)
      {
        this.alertifyService.error('API not started');
        this.isLogingIn = false;
      }else{
        this.alertifyService.error(error.message);
        this.isLogingIn = false;
      }
    });
  }
}
