import {Injectable} from '@angular/core';
import {HttpBackend, HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Request} from '../helpers/request.enum';
import {BehaviorSubject, Observable} from 'rxjs';
import {PaginatedResult} from '../_models/pagination';
import {Vehicle} from '../_models/vehicle';
import {map} from 'rxjs/operators';
import {User} from '../_models/user';
import {AuthService} from "./auth.service";
import {SessionUser} from "./session-user";


@Injectable({
  providedIn: 'root'
})

export class RequestService<T> {
  private readonly URL: string;
  private httpClientBackend: HttpClient;
  data = new BehaviorSubject<T>(null);
  user: SessionUser;


  constructor(private http: HttpClient,private httpBackend: HttpBackend, private authService: AuthService) {
    this.URL = Request.URL;
    this.httpClientBackend = new HttpClient(httpBackend);
  }


//-------------------GET CALLS-----------------------------------------------------------------------------
  getBackend(endpoint: string): Observable<T[]> {
    this.user = this.authService.user.value;
    return this.httpClientBackend
      .get<T[]>(  this.URL + endpoint,{headers: new HttpHeaders().append('Authorization', 'Bearer ' + this.user.Token).append('Access-Control-Allow-Origin', 'http://localhost:4200').append('Access-Control-Allow-Credentials',  'true')}  );
  }

  get(endpoint: string): Observable<T[]> {
    return this.http
      .get<T[]>(  this.URL + endpoint );
  }

  getQuery(endpoint: string, query: string): Observable<T[]> {
    return this.http
      .get<T[]>(  this.URL + endpoint + query  );
  }

  getQueryBackend(endpoint: string, query: string): Observable<T[]> {
    this.user = this.authService.user.value;
    return this.httpClientBackend
      .get<T[]>(  this.URL + endpoint + query,{headers: new HttpHeaders().append('Authorization', 'Bearer ' + this.user.Token).append('Access-Control-Allow-Origin', 'http://localhost:4200').append('Access-Control-Allow-Credentials',  'true')}  );
  }

  getSelectedUser(endpoint: string, needCarrier?, needClient?): Observable<User[]> {
    // @ts-ignore
    let userResult: User[];
    let params = new HttpParams();
    if (needCarrier != null) {
      params = params.append('needCarrier', needCarrier);
    }
    if (needClient != null) {
      params = params.append('needClient', needClient);
    }
    return this.http
      .get<User[]>(  this.URL + endpoint, {observe: 'response', params}).pipe(
        map(response => {
          userResult = response.body
          return userResult;
        })
      );
  }

  getPaginatedResult(page?, itemsPerPage?, endpoint?, globalParams?): Observable<PaginatedResult<T[]>> {
    const paginatedResult: PaginatedResult<T[]> = new PaginatedResult<T[]>();

    let params = new HttpParams();

    if (page != null && itemsPerPage != null)
    {
      params = params.append('pageNumber', page);
      params = params.append('pageSize', itemsPerPage);
    }

    if (globalParams != undefined)
    {
      if (globalParams.orderBy != null)
      {
        params = params.append('orderBy', globalParams.orderBy);
      }
      if (globalParams.type != null)
      {
        params = params.append('type', globalParams.type);
      }
      if (globalParams.name != null)
      {
        params = params.append('name', globalParams.name);
      }
      if (globalParams.needCarrier != null)
      {
        params = params.append('needCarrier', globalParams.needCarrier);
      }
      if (globalParams.isFavourite != null)
      {
        params = params.append('isFavourite', globalParams.isFavourite);
      }
      if (globalParams.activeCarrierFreights != null)
      {
        params = params.append('activeCarrierFreights', globalParams.activeCarrierFreights);
      }
      if (globalParams.statusStrArray != null)
      {
        params = params.append('statusStrArray', globalParams.statusStrArray);
      }
    }

    return this.http.get<T[]>(this.URL + endpoint, { observe: 'response', params}).pipe(
      map(response => {
        paginatedResult.result = response.body;
        if (response.headers.get('Pagination') != null) {
          paginatedResult.pagination = JSON.parse(response.headers.get('Pagination'));
        }
        return paginatedResult;

      })
    );
  }

  getById(id: number, endPoint: string): Observable<T> {
    return this.http.get<T>(  this.URL +  endPoint + id );
  }

  //------------------------------------------------------------------------------------------------------------
  //-------------------INSERT CALLS-----------------------------------------------------------------------------
  insert(data: any, endpoint: string) {
    return this.http.post<T>(Request.URL + endpoint, data);
  }

  setMainPhoto(id: number, userID: number)
  {
    console.log(Request.URL + '/Photo/' + id + '/setMain' + '?userId=' + userID);
    return this.http.post(Request.URL + '/Photo/' + id + '/setMain' + '?userId=' + userID, {});
  }

  setMainPhotoForVehicle(id: number, vehicleID: number)
  {
    return this.http.post(Request.URL + '/Photo/' + id + '/setMain' + '?vehicleId=' + vehicleID, {});
  }
  //------------------------------------------------------------------------------------------------------------
  //-------------------UPDATE CALLS-----------------------------------------------------------------------------
  update(id: number, data: any, endpoint: string) {
    return this.http.put<T>(this.URL + endpoint + id, data);
  }
  setFavouriteVehicle(id: number) {
    return this.httpClientBackend.put(this.URL + '/Vehicle/setFavourite/' + id, {});
  }

  //------------------------------------------------------------------------------------------------------------
  //-------------------DELETE CALLS-----------------------------------------------------------------------------
  delete(id: number, endpoint: string) {
    return this.http.delete(this.URL + endpoint + id);
  }

  deletePhoto(id: number)
  {
    return this.http.delete(Request.URL + '/Photo/' + id);
  }
  //------------------------------------------------------------------------------------------------------------





}
