import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {Vehicle} from '../../../_models/vehicle';
import {AuthService} from '../../../_services/auth.service';
import {SessionUser} from '../../../_services/session-user';
import {RequestService} from '../../../_services/request.service';
import {AlertifyService} from '../../../_services/alertify.service';
import {Request} from "../../../helpers/request.enum";

@Component({
  selector: 'app-vehicle-card',
  templateUrl: './vehicle-card.component.html',
  styleUrls: ['./vehicle-card.component.css']
})
export class VehicleCardComponent implements OnInit {
  @Input() vehicle: Vehicle;
  @Output() changeVehicleOrder = new EventEmitter<number>();
  sessionUser: SessionUser;
  message: string;
  icon: string;
  constructor(private authService: AuthService, private requestService: RequestService<any>, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.vehicle.isFavourite ? this.icon = 'favorite' : this.icon = 'favorite_border';
  }

  delete(id: number) {
    this.alertify.confirm('Are you sure you want to delete this vehicle?', () => {
      this.requestService.delete(id, Request.VehicleUpdateGetID).subscribe(() => {
        this.alertify.success('Vehicle succesfully deleted!');
        this.changeVehicleOrder.emit(id);
      }, error => {
        this.alertify.error(error);
      });
    }, () => {

    });
  }

  setFavourite(id: number) {
    if (!this.vehicle.isFavourite)
    {
      this.message = 'Vehicle set to favourite!';
      this.icon = 'favorite';
    } else {
      this.message = 'Vehicle is not favourite anymore!';
      this.icon = 'favorite_border';
    }

    this.requestService.setFavouriteVehicle(id).subscribe(() => {
      this.alertify.success(this.message);
    }, error => {
      this.alertify.error(error);
    });
    this.vehicle.isFavourite === true? this.vehicle.isFavourite = false : this.vehicle.isFavourite = true;
  }

}
