import { Component, OnInit } from '@angular/core';
import {AlertifyService} from "../../_services/alertify.service";
import {ActivatedRoute} from "@angular/router";
import {RequestService} from "../../_services/request.service";
import {CargoReservation} from "../../_models/cargo-reservation";
import {Freight} from "../../_models/freight";
import {Request} from "../../helpers/request.enum";
import {Pagination} from "../../_models/pagination";
import {SessionUser} from "../../_services/session-user";
import {AuthService} from "../../_services/auth.service";
import {CommentRating} from "../../_models/commentRating";
import {ChartDataSets, ChartOptions, ChartType} from "chart.js";
import {Label, SingleDataSet} from "ng2-charts";
import {StatusEnum} from "../../helpers/Status.Enum";
import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import {Carrier} from "../../_models/carrier";
import {StatisticsService} from "../../_services/statistics.service";

@Component({
  selector: 'app-dashboard-home',
  templateUrl: './dashboard-home.component.html',
  styleUrls: ['./dashboard-home.component.css']
})
export class DashboardHomeComponent implements OnInit {
  sessionUser: SessionUser;
  //client data
  cargoReservations: CargoReservation[];
  carriers: Carrier[];
  //carrier data
  myFreights: Freight[];
  commentRating: CommentRating[];
  pagination: Pagination;
  //statistic data
  monthsNumberList: number[];
  vehicleArray: Freight[] = [];
  numberOfOccurrences: number[] = [];


  constructor(private alertify: AlertifyService, private route: ActivatedRoute, private requestService: RequestService<any>,
              private authService: AuthService, private statisticsService: StatisticsService) { }

  ngOnInit(): void {
    this.sessionUser = this.authService.user.value;
    this.statisticsService.resetData();
    this.loadOnInit();
  }

  loadOnInit() {
    if (this.sessionUser.Role === 'Carrier') {
      this.requestService.get(Request.FreightGetNoPage).subscribe(res => {
        this.myFreights = res;
      },error => {},
        () => {
          if ( this.myFreights != undefined && this.myFreights.length != 0){
            this.statisticsService.doStatistic(this.myFreights);
          }else{
            this.statisticsService.assignDefaultValues();
          }
        });
      this.requestService.getQuery(Request.FreightGetNumberMonths, '?GetNumberMonths=true').subscribe(res => {
          this.monthsNumberList = res;
        },error => {},
        () => {
          if (this.monthsNumberList != undefined && this.monthsNumberList.length != 0){
            this.statisticsService.loadBarChartData(this.monthsNumberList);
          }else{
            this.statisticsService.assignDefaultValues();
          }
        });
    }
   if (this.sessionUser.Role === 'Client') {
     this.requestService.get(Request.CargoReservationGetNoPage).subscribe(res => {
         this.cargoReservations = res;
       },error => {},
       () => {
         this.statisticsService.doClientStatistic(this.cargoReservations);
       });

     this.requestService.get(Request.CarrierRecommend).subscribe(res => {
         this.carriers = res;
       });
    }

  }



}
