import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {AuthService} from "../../../_services/auth.service";
import {RequestService} from "../../../_services/request.service";
import {ActivatedRoute, Router} from "@angular/router";
import {AlertifyService} from "../../../_services/alertify.service";
import {CommonService} from "../../../_services/common.service";
import {Carrier} from "../../../_models/carrier";
import {Freight} from "../../../_models/freight";
import {StatusEnum} from "../../../helpers/Status.Enum";
import {Request} from "../../../helpers/request.enum";
import {CommentRatingInsertRequest} from "../../../_models/requests/commentRatingInsertRequest";
import {FreightInsertRequest} from "../../../_models/requests/freightInsertRequest";
import {RatingCarrierInsertRequest} from "../../../_models/requests/ratingCarrierInsertRequest";
import {CargoReservationInsertRequest} from "../../../_models/requests/cargo-reservation-insert-request";
import {CargoReservation} from "../../../_models/cargo-reservation";

@Component({
  selector: 'app-rate',
  templateUrl: './rate.component.html',
  styleUrls: ['./rate.component.css']
})
export class RateComponent implements OnInit {
  rateForm:FormGroup;
  cargoReservation: CargoReservation;
  commentRatingInsertRequest: CommentRatingInsertRequest = new CommentRatingInsertRequest();
  freightInsertRequest: FreightInsertRequest = new FreightInsertRequest();
  ratingCarrierInsertRequest: RatingCarrierInsertRequest = new RatingCarrierInsertRequest();
  cargoReservationInsertRequest: CargoReservationInsertRequest = new CargoReservationInsertRequest();


  constructor(private authService: AuthService, private requestService: RequestService<any>, private route: ActivatedRoute,
              private alertify: AlertifyService, private commonService: CommonService,private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.cargoReservation = data.cargoReservations;
    });
    this.createRatingForm();
  }

  createRatingForm() {
    this.rateForm = this.formBuilder.group({
      rating: ['', Validators.required],
      comment: ['', Validators.required]
    });
  }

  rate() {
    this.commentRatingInsertRequest = Object.assign({}, this.rateForm.value);
    this.commentRatingInsertRequest.clientID = this.cargoReservation.clientID;
    this.commentRatingInsertRequest.freightID = this.cargoReservation.freightID;
    this.freightInsertRequest.statusStr = StatusEnum.Rated;
    this.cargoReservationInsertRequest.statusStr = StatusEnum.Rated;
    this.ratingCarrierInsertRequest.carrierID = this.cargoReservation.freight.carrierID;
    this.requestService.insert(this.commentRatingInsertRequest, Request.CommentRatingInsertGet).subscribe(() => {
      this.alertify.success('Rating successful!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.requestService.insert(this.ratingCarrierInsertRequest, Request.RatingCarrierInsertGet).subscribe();
      this.router.navigate(['/dashboard/reservations/myReservations']);
    });
    this.requestService.update(this.cargoReservation.freightID, this.freightInsertRequest, Request.FreightUpdateGet).subscribe();
    this.requestService.update(this.cargoReservation.cargoReservationID, this.cargoReservationInsertRequest, Request.CargoReservationUpdateGetID).subscribe();
  }
}
