import {AfterViewInit, Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {City} from '../../../_models/city';
import {RequestService} from '../../../_services/request.service';
import {ActivatedRoute, Router} from "@angular/router";
import {User} from "../../../_models/user";
import {Cargo} from "../../../_models/cargo";
import {ExtraServices} from "../../../_models/extra-services";
import {Request} from "../../../helpers/request.enum";
import {CargoReservationInsertRequest} from "../../../_models/requests/cargo-reservation-insert-request";
import {CargoReservation} from "../../../_models/cargo-reservation";
import {AlertifyService} from "../../../_services/alertify.service";
import {CargoInsertRequest} from "../../../_models/requests/cargoInsertRequest";

@Component({
  selector: 'app-reservations-add',
  templateUrl: './reservations-add.component.html',
  styleUrls: ['./reservations-add.component.css']
})
export class ReservationsAddComponent implements OnInit {
  addReservationForm: FormGroup;
  cities: City[];
  users: User[];
  cargos: Cargo[];
  cargoInsert: CargoInsertRequest = new CargoInsertRequest();
  extraServices: ExtraServices[];
  cargoReservation: CargoReservationInsertRequest = new CargoReservationInsertRequest();
  optional: string; //string change when the call is for specific carrier or global carriers
  servicesAvailable: boolean = false; //hide/show extra services dropdown

  constructor(private formBuilder: FormBuilder, private requestService: RequestService<any>, private route: ActivatedRoute, private alertify: AlertifyService,
              private router: Router) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.users = data.users;
    });
    this.requestService.get('/City').subscribe(result => {
      this.cities = result;
    });
    this.requestService.get(Request.CargoGETNOPAGING).subscribe(result => {
      this.cargos = result;
    }, error => {
      this.alertify.error("You don't have any cargos!")
    });
    this.createAddForm();
    this.users.length == 1 ? this.optional = '(required)' : this.optional = '(optional)';
  }

  createAddForm() {
    this.addReservationForm = this.formBuilder.group({
      startDateTransport: [null, Validators.required],
      endDateTransport: [null, Validators.required],
      startLocation: ['', Validators.required],
      endLocation: ['', Validators.required],
      cargoID: ['', Validators.required],
      carrierID: ['', this.users.length == 1 ? Validators.required : null],
      extraServicesID: ['']
    });
  }

  reset() {
    this.servicesAvailable = false;
    this.extraServices.splice(0);
  }

  loadExtraServices(id: number) {
    this.servicesAvailable = true;
    this.requestService.getQueryBackend(Request.ExtraServicesGET,'?CarrierID=' + id).subscribe(result => {
      this.extraServices = result;
    });
  }

  add(): void {
    this.cargoReservation = Object.assign({}, this.addReservationForm.value);
    this.cargoInsert.isUsed = true;
    this.requestService.update(this.cargoReservation.cargoID,this.cargoInsert, Request.CargoUpdateGetID ).subscribe();
    this.requestService.insert(this.cargoReservation, Request.CargoReservationInsertGet).subscribe(() => {
      this.alertify.success('Reservation added successfully!');
    }, error => {
      this.alertify.error(error);
    }, () => {
      this.router.navigate(['/dashboard/reservations/myReservations']);
    });
  }
}
