import { Component, OnInit } from '@angular/core';
import {Request} from "../../helpers/request.enum";
import {RequestService} from "../../_services/request.service";
import {ActivatedRoute} from "@angular/router";
import {CargoReservation} from "../../_models/cargo-reservation";
import autoTable from "jspdf-autotable";
const { jsPDF } = require("jspdf");
require('jspdf-autotable');

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {
  cargoReservations: CargoReservation[];
  totalPrice: number = 0;
  CargoHasData: boolean;
  CargoNoData: boolean;
  constructor(private requestService: RequestService<any>, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.requestService.getQuery(Request.CargoReservationGetNoPage, '?ActiveCarrierFreights=true&CompletedRatedFreights=true').subscribe(res => {
        this.cargoReservations = res;
      }, error => {}
      ,() => {
        for (let cargo of this.cargoReservations){
          this.totalPrice += cargo.freight?.price;
        }
        this.cargoReservations.length === 0 ? this.CargoNoData = true: this.CargoHasData = true;
      });
  }

  DownloadReport(){
    let totalPriceSum: number = 0;
    let row : any[] = []
    let rowD : any[] = []
    let col=[['Start location','End location','Client','Price', 'Status' ] ]; // initialization for headers
    let title = "Completed freights report" // title of report
    for(let a=0;a <this.cargoReservations.length;a++){
      row.push(this.cargoReservations[a].startLocation)
      row.push(this.cargoReservations[a].endLocation)
      row.push(this.cargoReservations[a].client?.firstName)
      row.push(this.cargoReservations[a].freight?.price)
      row.push(this.cargoReservations[a].freight?.statusStr)
      rowD.push(row);
      row =[];
      totalPriceSum+= this.cargoReservations[a].freight?.price;
    }
    let fot=[['','','','Total price: ' + totalPriceSum + '$']];
    this.getReport(col , rowD , title, fot );
  }

  getReport(col: any[], rowD: any[], title: any, fot: any[]) {
    const totalPagesExp = "{total_pages_count_string}";
    const pdf = new jsPDF('l', 'pt', 'legal');
    pdf.setTextColor(51, 156, 255);
    pdf.text("" + title, 435,100);  //
    pdf.setLineWidth(1.5);
    pdf.line(5, 107, 995, 107)
    var pageContent = function (data) {
      // HEADER

      // FOOTER
      var str = "Page " + data.pageCount;
      // Total page number plugin only available in jspdf v1.0+
      if (typeof pdf.putTotalPages === 'function') {
        str = str + " of " + totalPagesExp;
      }
      pdf.setFontSize(10);
      var pageHeight = pdf.internal.pageSize.height || pdf.internal.pageSize.getHeight();
      pdf.text(str, data.settings.margin.left, pageHeight - 10); // showing current page number
    };
    pdf.autoTable = autoTable(pdf, {
      head: col,
      body: rowD,
      margin: {top: 110},
      foot: fot,
      headStyles: {
        halign: 'center',
        valign: 'middle',
      },
      bodyStyles: {
        halign: 'center',
        valign: 'middle'
      },
      footStyles: {
        halign: 'center',
        valign: 'middle'
      }
    });
   /* pdf.autoTable = autoTable(pdf, {
      html: '#myTable',
      theme: 'grid'
    });*/
    /*pdf.autoTable(col, rowD,
      {
        addPageContent: pageContent,
        margin: { top: 110 },
      }, col);*/
    //for adding total number of pages // i.e 10 etc
    if (typeof pdf.putTotalPages === 'function') {
      pdf.putTotalPages(totalPagesExp);
    }
    pdf.save(title + '.pdf');
  }

}
