import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Vehicle} from '../../../_models/vehicle';
import {AlertifyService} from '../../../_services/alertify.service';
import {ActivatedRoute} from '@angular/router';
import {RequestService} from '../../../_services/request.service';
import {VehicleModel} from '../../../_models/vehicle-model';
import {VehicleType} from '../../../_models/vehicle-type';
import {Photo} from '../../../_models/photo';
import {Request} from "../../../helpers/request.enum";

@Component({
  selector: 'app-vehicles-detail',
  templateUrl: './vehicles-detail.component.html',
  styleUrls: ['./vehicles-detail.component.css']
})
export class VehiclesDetailComponent implements OnInit {
  updateVehicleForm: FormGroup;
  vehicle: Vehicle;
  vehicleValidation: Vehicle;
  vehicleID: number;
  vehiclePhotoURL: string;
  vehiclePhotos: Photo[];
  vehicleModels: VehicleModel[];
  vehicleTypes: VehicleType[];
  constructor(private alertify: AlertifyService, private route: ActivatedRoute, private requestService: RequestService<any>,
              private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.vehicle = data.vehicleDetail;
    });
    this.vehicleID = this.vehicle.vehicleID;
    this.vehiclePhotoURL = this.vehicle.vehiclePhotoUrl;
    this.vehiclePhotos = this.vehicle.photos;
    this.requestService.get(Request.VehicleModelInsertGet).subscribe(result => {
      this.vehicleModels = result;
    });
    this.requestService.get(Request.VehicleTypeInsertGet).subscribe(result => {
      this.vehicleTypes = result;
    });
    this.createUpdateForm();
    this.vehicleValidation = Object.assign({}, this.updateVehicleForm.value);
  }

  update() {
    this.vehicle = Object.assign({}, this.updateVehicleForm.value);
    this.vehicle.vehiclePhotoUrl = this.vehiclePhotoURL;
    this.vehicle.photos = this.vehiclePhotos;
    if (JSON.stringify(this.vehicleValidation) === JSON.stringify(this.vehicle))
    {
      this.alertify.error('The values are the same as the old ones! (Umbasa)');
    } else {
      this.updateVehicleForm.reset(this.vehicle);
      this.vehicleValidation = this.vehicle;
      this.requestService.update(this.vehicleID, this.vehicle, Request.VehicleUpdateGetID).subscribe(next => {
        this.alertify.success('Vehicle updated successfully!');
      }, error => {
        this.alertify.error(error);
      });
    }
    this.updateVehicleForm.reset(this.vehicle);
  }

  createUpdateForm() {
    this.updateVehicleForm = this.formBuilder.group({
      licencePlate: [this.vehicle.licencePlate, Validators.required],
      seatingCapacity: [this.vehicle.seatingCapacity, Validators.required],
      trunkVolume: [this.vehicle.trunkVolume, Validators.required],
      yearOfManufacture: [this.vehicle.yearOfManufacture, Validators.required],
      vehicleModelID: [this.vehicle.vehicleModelID, Validators.required],
      vehicleTypeID: [this.vehicle.vehicleTypeID, Validators.required],
      vehicleDetails: this.formBuilder.group({
        maxHeight: [this.vehicle.vehicleDetails === null ? 0 : this.vehicle.vehicleDetails.maxHeight, Validators.required],
        maxWeight: [this.vehicle.vehicleDetails === null ? 0 : this.vehicle.vehicleDetails.maxWeight, Validators.required],
        maxLength: [this.vehicle.vehicleDetails === null ? 0 : this.vehicle.vehicleDetails.maxLength, Validators.required],
        maxWidth: [this.vehicle.vehicleDetails === null ? 0 : this.vehicle.vehicleDetails.maxWidth, Validators.required],
        price_per_km: [this.vehicle.vehicleDetails === null ? 0 : this.vehicle.vehicleDetails.price_per_km, Validators.required],
        description: [this.vehicle.vehicleDetails === null ? '' : this.vehicle.vehicleDetails.description, Validators.required],
      }),
    });
  }

  updateMainPhoto(photoUrl: string) {
    this.vehicle.vehiclePhotoUrl = photoUrl;
    this.vehiclePhotoURL = photoUrl;
  }
}
