import { Injectable } from '@angular/core';
import {HubConnection, HubConnectionBuilder} from "@microsoft/signalr";
import {AuthService} from "./auth.service";
import {Notification} from "../_models/notification";
import {BehaviorSubject, Subject} from "rxjs";
import {GpsLocation} from "../_models/signalR/gps-location";
import {GpsHelperService} from "../helpers/gps-helper.service";

@Injectable({
  providedIn: 'root'
})
export class GpsService {
  private connection: HubConnection;
  public gpsLocation: GpsLocation = new GpsLocation();

  constructor(private authService: AuthService, private gpsHelper: GpsHelperService) { }


  public async StopConnection() {
    try {
      await this.connection.stop();
      console.log("Connection stopped");
    }
    catch (err){
      console.log(err);
    }
  }

  public async StartConnection() {
    try {
      await this.connection.start();
      console.log("Connection started");
    }
    catch (err){
      console.log(err);
    }
  }

  InitializeConnection() {
    this.connection = new HubConnectionBuilder().withUrl("http://localhost:52391/gps", {
      accessTokenFactory: () => this.authService.user.value.Token,
    }).withAutomaticReconnect().build();

    this.connection.on("OnPushGPSLocation", (res: GpsLocation) => {
     this.gpsHelper.item.next(res);
    });
  }

  getPosition() {
    // Simple wrapper
    return new Promise((res, rej) => {

      navigator.geolocation.getCurrentPosition(res, rej);
    });
  }

  async CarrierInvokeGps(cargoId){
    let position = await this.getPosition();
    // @ts-ignore
    this.gpsLocation.lat = position.coords.latitude;
    // @ts-ignore
    this.gpsLocation.lng = position.coords.longitude;
    await this.connection.invoke("PushGPSLocation", this.gpsLocation,cargoId).catch(err => console.error(err));
  }


  Destroy() {
    /*this.notiService.emitter.unsubscribe();
    this.notiService.data.unsubscribe();
    //this.notiService.item.unsubscribe();

    this.notiService.emitter = new Subject<number>();
    this.notiService.data = new BehaviorSubject<Notification[]>(null);
    //this.notiService.item = new BehaviorSubject<Notification>(null);*/
  }
}
