import {Injectable} from '@angular/core';
import {User} from '../_models/user';
import {ActivatedRouteSnapshot, Resolve, Router} from '@angular/router';
import {AlertifyService} from '../_services/alertify.service';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import {AuthService} from '../_services/auth.service';
import {RequestService} from '../_services/request.service';
import {Vehicle} from '../_models/vehicle';
import {pageNumber, pageSize} from "../helpers/globals";



@Injectable()
export class VehicleResolver implements Resolve<Vehicle[]> {
  constructor(private requestService: RequestService<any>,
              private authService: AuthService,
              private router: Router, private alertify: AlertifyService){}

  resolve(route: ActivatedRouteSnapshot): Observable<Vehicle[]> {
    return this.requestService.getPaginatedResult(pageNumber, pageSize).pipe(
      catchError(error => {
        this.alertify.error('Problem retrieving your data');
        this.router.navigate(['/home']);
        return of(null);
      })
    );
  }

}
